import React from "react";
import { Card, CardContainer, SelectingContainer } from "./practical.styles";
import { styled } from "../../../../stitches.config";

export function SelectConcentration(props) {
  return (
    <SelectingContainer>
      <h4 style={{ textAlign: "center", marginBottom: "25px" }}>
        Select ??? Concentration
      </h4>
      <CardContainer>
        <ConCard
          style={{ backgroundColor: "#b0bec5" }}
          onClick={() => {
            props.setSelectedCon("1");
            props.changeCurrentStep();
          }}
        >
          <ConText>1:1</ConText>
        </ConCard>
        <ConCard
          style={{ backgroundColor: "#90a4ae" }}
          onClick={() => {
            props.setSelectedCon("2");
            props.changeCurrentStep();
          }}
        >
          <ConText>1:1.5</ConText>
        </ConCard>
        <ConCard
          style={{ backgroundColor: "#78909c" }}
          onClick={() => {
            props.setSelectedCon("3");
            props.changeCurrentStep();
          }}
        >
          <ConText>1:2</ConText>
        </ConCard>
      </CardContainer>
    </SelectingContainer>
  );
}

const ConText = styled("h2", {
  textAlign: "center",
  margin: "auto",
});

const ConCard = styled(Card, {
  backgroundColor: "#ef5350",
  display: "flex",
  alignItems: "center",
});
