import React, { useEffect, useState } from "react";
import { WireLines } from "./components/lines";
import { SolarImage } from "./components/solar";
import { Points } from "./components/points";
import { PV } from "./components/pv";
import { Converter } from "./components/converter";
import { LoadsImage } from "./components/loads";
import { H2 } from "./components/h2";
import { FuelCell } from "./components/fuelCell";
import { Inverter } from "./components/inverter";
import { Electrolyzer } from "./components/electrolyzer";
import { Battery } from "./components/battery";
import { SidePanel } from "./components/sidePanel";
import { ContinueButton } from "./hydrogenStorage.styles";

export function Practical(props) {
  const [pvShow, setPvShow] = useState(false);
  const [batteryShow, setBatteryShow] = useState(false);
  const [inverterShow, setInverterShow] = useState(false);
  const [converterShow, setConverterShow] = useState(false);
  const [h2Show, seth2Show] = useState(false);
  const [electrolyzerShow, setElectrolyzerShow] = useState(false);
  const [fuelCellShow, setfuelCellShow] = useState(false);
  const onComponentSelected = (component) => {
    if (component == "pv") {
      setPvShow(true);
    } else if (component == "battery") {
      setBatteryShow(true);
    } else if (component == "h2") {
      seth2Show(true);
    } else if (component == "inverter") {
      setInverterShow(true);
    } else if (component == "converter") {
      setConverterShow(true);
    } else if (component == "fuelCell") {
      setfuelCellShow(true);
    } else if (component == "electrolyzer") {
      setElectrolyzerShow(true);
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          height: "82%",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            height: "100%",
            width: "100%",
          }}
        >
          <div
            style={{
              flex: 19,
              border: "solid",
              borderWidth: "1px",
              background: "white",
              paddingTop: "15px",
            }}
          >
            <WireLines />
            <SolarImage />
            <Points />
            {pvShow && <PV />}
            {converterShow && <Converter />}
            {h2Show && <H2 />}
            {inverterShow && <Inverter />}
            {fuelCellShow && <FuelCell />}
            {electrolyzerShow && <Electrolyzer />}
            {batteryShow && <Battery />}
            <LoadsImage />
            <ContinueButton
              style={{
                position: "absolute",
                bottom: "25px",
                left: "655px",
              }}
              onClick={() => props.onStepChange(4)}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Next
            </ContinueButton>
          </div>

          <div
            style={{
              flex: 6,
              backgroundColor: "#A9A9A9",
              paddingTop: "20px",
            }}
          >
            <div style={{ marginLeft: "10px" }}>
              <label style={{ marginLeft: "10px", marginBottom: "10px" }}>
                Select the components
              </label>
              <SidePanel onComponentClicked={onComponentSelected} />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
