import React from 'react';
export function SuccessMessage(props) {
    return (
        <div>
            <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                <div style={{ backgroundColor: '#0072b1', width: '1015px', height: '580px' }}>
                    <div style={{ color: 'white', fontSize: '38px', padding: '80px 40px', textAlign: 'center' }}>
                        <div style={{ textAlign: 'center' }}>Congratulations!
                        </div><br></br>
                        {props.message}</div>
                </div>
            </div>
        </div>
    );
}


