import React, { useState } from 'react';
import IntroSrc from "./assets/main.jpg";
import { ContinueButton } from './hydrogenStorage.styles';

export function Questions(props) {
    const [checked1, setChecked1] = useState(false);
    const [checked2, setChecked2] = useState(false);
    const [checked3, setChecked3] = useState(false);
    const handleOnChange1 = () => {
        setChecked1(!checked1);
    };
    const handleOnChange2 = () => {
        setChecked2(!checked2);
    };
    const handleOnChange3 = () => {
        setChecked3(!checked3);
    };
    const onSubmitClicked = () => {
        if (checked1 && checked3 && !checked2) {
          props.onStepChange(5);
        } else {
          props.onStepChange(6);
        }
    }
    return (
      <div>
        <div
          style={{
            width: "1020px",
            position: "absolute",
            border: "solid",
            borderWidth: "0.1rem",
            borderColor: "white",
            left: "10px",
            bottom: "10px",
          }}
        >
          <img src={IntroSrc} style={{ width: "100%" }} />
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              position: "absolute",
              top: "0%",
              left: "0%",
              width: "100%",
              height: "100%",
            }}
          >
            <div
              style={{
                flex: 2,
                backgroundColor: "black",
                opacity: "0.7",
                color: "white",
              }}
            >
              <div style={{ textAlign: "center", marginTop: "20px" }}>
                <h3>
                  PLEASE SELECT ALL THE STATEMENTS <br></br> THAT ARE TRUE{" "}
                </h3>
              </div>
            </div>
            <div style={{ flex: 10 }}>
              <div style={{ width: "80%", position: "absolute", left: "10%" }}>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    marginTop: "40px",
                  }}
                >
                  <div style={{ flex: 5, backgroundColor: "white" }}>
                    <div
                      style={{
                        textAlign: "left",
                        border: "solid",
                        borderWidth: "0.2rem",
                        borderColor: "black",
                        padding: "8px",
                      }}
                    >
                      <div style={{ fontSize: "19px", fontWeight: "450" }}>
                        Solar panels generate direct current (DC power)
                        electricity. This electricity undergoes conversion by
                        the inverter, transitioning from direct current (DC
                        power) to alternating current (AC power).
                        Simultaneously, an electrolyser utilizes water to
                        produce hydrogen and oxygen; the hydrogen is stored in
                        the tank, while the released oxygen disperses into the
                        air.
                      </div>
                    </div>
                  </div>
                  <div style={{ flex: 1, position: "relative" }}>
                    {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                    <div
                      style={{
                        position: "absolute",
                        bottom: "30%",
                        left: "30%",
                      }}
                    >
                      <input
                        type="checkbox"
                        checked={checked1}
                        onChange={handleOnChange1}
                        style={{ width: "25px", height: "25px" }}
                        id="html"
                      ></input>
                    </div>
                  </div>
                </div>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    marginTop: "40px",
                  }}
                >
                  <div style={{ flex: 5, backgroundColor: "white" }}>
                    <div
                      style={{
                        textAlign: "left",
                        border: "solid",
                        borderWidth: "0.2rem",
                        borderColor: "black",
                        padding: "8px",
                      }}
                    >
                      <div style={{ fontSize: "19px", fontWeight: "450" }}>
                        The hydrogen stored in the tank combines with air,
                        serving as fuel for electricity generation. This
                        generated electricity is then conveyed to the inverter,
                        where its current is converted and integrated into the
                        home&#39;s electrical system. This process serves to
                        substitute for solar electricity that isn&#39;t produced
                        during nighttime.
                      </div>
                    </div>
                  </div>
                  <div style={{ flex: 1, position: "relative" }}>
                    {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                    <div
                      style={{
                        position: "absolute",
                        bottom: "30%",
                        left: "30%",
                      }}
                    >
                      <input
                        type="checkbox"
                        checked={checked2}
                        onChange={handleOnChange2}
                        style={{ width: "25px", height: "25px" }}
                        id="html"
                      ></input>
                    </div>
                  </div>
                </div>
                
              </div>
              <div
                style={{ position: "absolute", right: "50px", bottom: "40px" }}
              >
                <ContinueButton
                  onClick={onSubmitClicked}
                  className="continue-button"
                  role="button"
                >
                  Submit
                </ContinueButton>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
}


