import React, { useEffect, useState } from "react";
import Draggable from "react-draggable";
import Video from "./1.mp4"
import Video2 from "./2.mp4"
import Video3 from "./3.mp4"
import Video4 from "./4.mp4"
import Video5 from "./5.mp4"
import Video6 from "./6.mp4"
import { Boxes } from "./boxes";
import { ContinueButton } from "./video.styles";

const WIDTHPX = '150px'
const HEIGHTPX = '150px'

export function Practical(props) {
  const videoOneRef = React.createRef();
  const videoTwoRef = React.createRef();
  const videoThreeRef = React.createRef();
  const videoFourRef = React.createRef();
  const videoFiveRef = React.createRef();
  const videoSixRef = React.createRef();
  const [isPlaying, setIsPlaying] = useState([false, false, false, false, false, false]);
  const [videoOne, setVideoOne] = useState(
    {

      x: 435,
      y: 380,
      correctPoint: false,
    })
  const [videoTwo, setVideoTwo] = useState(
    {
      x: 100,
      y: 380,
      correctPoint: false,
    })
  const [videoThree, setVideoThree] = useState(
    {
      x: 760,
      y: 200,
      correctPoint: false,
    })
  const [videoFour, setVideoFour] = useState(
    {
      x: 760,
      y: 380,
      correctPoint: false,
    })
  const [videoFive, setVideoFive] = useState(
    {
      x: 100,
      y: 200,
      correctPoint: false,
    })
  const [videoSix, setVideoSix] = useState(
    {
      x: 435,
      y: 200,
      correctPoint: false,
    })
  const handleDragVideoOne = (e, ui,) => {
    const tempV = { ...videoOne };
    tempV.x = tempV.x + ui.deltaX;
    tempV.y = tempV.y + ui.deltaY;
    videoOneRef.current.pause();
    setVideoOne({ ...tempV });
  };
  const handleVideoOneStop = (e, position) => {
    const { x, y } = { ...position };
    if (y < 73 && x < 96 && !isPlaying[0]) {
      const tempV = { ...videoOne };
      tempV.x = 20
      tempV.y = 7
      tempV.correctPoint = true
      setVideoOne({ ...tempV });
    } else if (!isPlaying[0]) {
      // alert("Select correct point");
    }
    videoOneRef.current.play();
  };
  const handleDragVideoTwo = (e, ui,) => {
    const tempV = { ...videoTwo };
    tempV.x = tempV.x + ui.deltaX;
    tempV.y = tempV.y + ui.deltaY;
    videoTwoRef.current.pause();
    setVideoTwo({ ...tempV });
  };
  const handleVideoTwoStop = (e, position) => {
    const { x, y } = { ...position };
    if (y < 73 && x > 107 && x < 264 && !isPlaying[1]) {
      const tempV = { ...videoTwo };
      tempV.x = 184
      tempV.y = 7
      tempV.correctPoint = true
      setVideoTwo({ ...tempV });
    } else if (!isPlaying[1]) {
      // alert("Select correct point");
    }
    videoTwoRef.current.play();
  };
  const handleDragVideoThree = (e, ui,) => {
    const tempV = { ...videoThree };
    tempV.x = tempV.x + ui.deltaX;
    tempV.y = tempV.y + ui.deltaY;
    videoThreeRef.current.pause();
    setVideoThree({ ...tempV });
  };
  const handleVideoThreeStop = (e, position) => {
    const { x, y } = { ...position };
    if (y < 73 && x > 272 && x < 429 && !isPlaying[2]) {
      const tempV = { ...videoThree };
      tempV.x = 350
      tempV.y = 7
      tempV.correctPoint = true
      setVideoThree({ ...tempV });
    } else if (!isPlaying[2]) {
      // alert("Select correct point");
    }
    videoThreeRef.current.play();
  };

  const handleDragVideoFour = (e, ui,) => {
    const tempV = { ...videoFour };
    tempV.x = tempV.x + ui.deltaX;
    tempV.y = tempV.y + ui.deltaY;
    videoFourRef.current.pause();
    setVideoFour({ ...tempV });
  };
  const handleVideoFourStop = (e, position) => {
    const { x, y } = { ...position };
    if (y < 73 && x > 438 && x < 593 && !isPlaying[3]) {
      const tempV = { ...videoFour };
      tempV.x = 514
      tempV.y = 7
      tempV.correctPoint = true
      setVideoFour({ ...tempV });
    } else if (!isPlaying[3]) {
      // alert("Select correct point");
    }
    videoFourRef.current.play();
  };

  const handleDragVideoFive = (e, ui,) => {
    const tempV = { ...videoFive };
    tempV.x = tempV.x + ui.deltaX;
    tempV.y = tempV.y + ui.deltaY;
    videoFiveRef.current.pause();
    setVideoFive({ ...tempV });
  };
  const handleVideoFiveStop = (e, position) => {
    const { x, y } = { ...position };
    if (y < 73 && x > 602 && x < 759 && !isPlaying[4]) {
      const tempV = { ...videoFive };
      tempV.x = 680
      tempV.y = 7
      tempV.correctPoint = true
      setVideoFive({ ...tempV });
    } else if (!isPlaying[4]) {
      // alert("Select correct point");
    }
    videoFiveRef.current.play();
  };

  const handleDragVideoSix = (e, ui,) => {
    const tempV = { ...videoSix };
    tempV.x = tempV.x + ui.deltaX;
    tempV.y = tempV.y + ui.deltaY;
    videoSixRef.current.pause();
    setVideoSix({ ...tempV });
  };
  const handleVideoSixStop = (e, position) => {
    const { x, y } = { ...position };
    if (y < 73 && x > 767 && !isPlaying[5]) {
      const tempV = { ...videoSix };
      tempV.x = 844
      tempV.y = 7
      tempV.correctPoint = true
      setVideoSix({ ...tempV });
    } else if (!isPlaying[5]) {
      // alert("Select correct point");
    }
    videoSixRef.current.play();
  };
  const handlePlay = (index) => {
    let tempIsPlaying = [...isPlaying]
    tempIsPlaying[index] = true;
    setIsPlaying(tempIsPlaying);
  };

  const handlePause = (index) => {
    let tempIsPlaying = [...isPlaying]
    tempIsPlaying[index] = false;
    setIsPlaying(tempIsPlaying);
  };

  const handleNextClick = () => {
    if (videoOne.correctPoint && videoTwo.correctPoint && videoThree.correctPoint && videoFour.correctPoint &&
      videoFive.correctPoint && videoSix.correctPoint) {
      props.onStepChange(4)
    } else {
      alert("Select correct points");
    }
  };

  useEffect(() => {
    // Remove specific controls
    const videoElement = document.querySelector('video');
    videoElement.removeAttribute('controlsList');
    // You can further manipulate controls here if needed

    // Clean up the effect
    return () => {
      // Cleanup code, if necessary
    };
  }, []);

  return (
    <div>
      <div
        style={{
          width: "1020px",
          height: "82%",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
          background: '#d3d3d3 '
        }}
      >
        <Draggable
          bounds="parent"
          disabled={videoOne.correctPoint}
          defaultPosition={{ x: videoOne.x, y: videoOne.y }}
          position={{ x: isPlaying[0] ? 0 : videoOne.x, y: isPlaying[0] ? 0 : videoOne.y }}
          onDrag={(e, ui) => handleDragVideoOne(e, ui)}
          onStop={(e, ui) => handleVideoOneStop(e, ui)}
        >
          <div style={{ width: isPlaying[0] ? '100%' : WIDTHPX, height: isPlaying[0] ? '100%' : HEIGHTPX, position: "absolute", zIndex: isPlaying[0] ? 2 : 1 }}>
            <video style={{ width: isPlaying[0] ? '100%' : WIDTHPX, height: isPlaying[0] ? '100%' : HEIGHTPX }}
              onPlay={() => handlePlay(0)}
              onPause={() => handlePause(0)}
              ref={videoOneRef}
              controls >
              <source src={Video} type="video/mp4" />
            </video>
          </div>
        </Draggable>

        <Draggable
          bounds="parent"
          disabled={videoTwo.correctPoint}
          defaultPosition={{ x: videoTwo.x, y: videoTwo.y }}
          position={{ x: isPlaying[1] ? 0 : videoTwo.x, y: isPlaying[1] ? 0 : videoTwo.y }}
          onDrag={(e, ui) => handleDragVideoTwo(e, ui)}
          onStop={(e, ui) => handleVideoTwoStop(e, ui)}
        >
          <div style={{ width: isPlaying[1] ? '100%' : WIDTHPX, height: isPlaying[1] ? '100%' : HEIGHTPX, position: "absolute", zIndex: isPlaying[1] ? 2 : 1 }}>
            <video style={{ width: isPlaying[1] ? '100%' : WIDTHPX, height: isPlaying[1] ? '100%' : HEIGHTPX }}
              onPlay={() => handlePlay(1)}
              onPause={() => handlePause(1)}
              ref={videoTwoRef}
              controls >
              <source src={Video2} type="video/mp4" />
            </video>
          </div>
        </Draggable>

        <Draggable
          bounds="parent"
          disabled={videoThree.correctPoint}
          defaultPosition={{ x: videoThree.x, y: videoThree.y }}
          position={{ x: isPlaying[2] ? 0 : videoThree.x, y: isPlaying[2] ? 0 : videoThree.y }}
          onDrag={(e, ui) => handleDragVideoThree(e, ui)}
          onStop={(e, ui) => handleVideoThreeStop(e, ui)}
        >
          <div style={{ width: isPlaying[2] ? '100%' : WIDTHPX, height: isPlaying[2] ? '100%' : HEIGHTPX, position: "absolute", zIndex: isPlaying[2] ? 2 : 1 }}>
            <video style={{ width: isPlaying[2] ? '100%' : WIDTHPX, height: isPlaying[2] ? '100%' : HEIGHTPX }}
              onPlay={() => handlePlay(2)}
              onPause={() => handlePause(2)}
              ref={videoThreeRef}
              controls>
              <source src={Video3} type="video/mp4" />
            </video>
          </div>
        </Draggable>

        <Draggable
          bounds="parent"
          disabled={videoFour.correctPoint}
          defaultPosition={{ x: videoFour.x, y: videoFour.y }}
          position={{ x: isPlaying[3] ? 0 : videoFour.x, y: isPlaying[3] ? 0 : videoFour.y }}
          onDrag={(e, ui) => handleDragVideoFour(e, ui)}
          onStop={(e, ui) => handleVideoFourStop(e, ui)}
        >
          <div style={{ width: isPlaying[3] ? '100%' : WIDTHPX, height: isPlaying[3] ? '100%' : HEIGHTPX, position: "absolute", zIndex: isPlaying[3] ? 2 : 1 }}>
            <video style={{ width: isPlaying[3] ? '100%' : WIDTHPX, height: isPlaying[3] ? '100%' : HEIGHTPX }}
              onPlay={() => handlePlay(3)}
              onPause={() => handlePause(3)}
              ref={videoFourRef}
              controls >
              <source src={Video4} type="video/mp4" />
            </video>
          </div>
        </Draggable>

        <Draggable
          bounds="parent"
          disabled={videoFive.correctPoint}
          defaultPosition={{ x: videoFive.x, y: videoFive.y }}
          position={{ x: isPlaying[4] ? 0 : videoFive.x, y: isPlaying[4] ? 0 : videoFive.y }}
          onDrag={(e, ui) => handleDragVideoFive(e, ui)}
          onStop={(e, ui) => handleVideoFiveStop(e, ui)}
        >
          <div style={{ width: isPlaying[4] ? '100%' : WIDTHPX, height: isPlaying[4] ? '100%' : HEIGHTPX, position: "absolute", zIndex: isPlaying[4] ? 2 : 1 }}>
            <video style={{ width: isPlaying[4] ? '100%' : WIDTHPX, height: isPlaying[4] ? '100%' : HEIGHTPX }}
              onPlay={() => handlePlay(4)}
              onPause={() => handlePause(4)}
              ref={videoFiveRef}
              controls>
              <source src={Video5} type="video/mp4" />
            </video>
          </div>
        </Draggable>

        <Draggable
          bounds="parent"
          disabled={videoSix.correctPoint}
          defaultPosition={{ x: videoSix.x, y: videoSix.y }}
          position={{ x: isPlaying[5] ? 0 : videoSix.x, y: isPlaying[5] ? 0 : videoSix.y }}
          onDrag={(e, ui) => handleDragVideoSix(e, ui)}
          onStop={(e, ui) => handleVideoSixStop(e, ui)}
        >
          <div style={{ width: isPlaying[5] ? '100%' : WIDTHPX, height: isPlaying[5] ? '100%' : HEIGHTPX, position: "absolute", zIndex: isPlaying[5] ? 2 : 1 }}>
            <video style={{ width: isPlaying[5] ? '100%' : WIDTHPX, height: isPlaying[5] ? '100%' : HEIGHTPX }}
              onPlay={() => handlePlay(5)}
              onPause={() => handlePause(5)}
              ref={videoSixRef}
              controls>
              <source src={Video6} type="video/mp4" />
            </video>
          </div>
        </Draggable>
        {/* <div hidden={!isPlaying[0]}>
          <video style={{ width: '100%', height: '100%', zIndex: 1, position: 'absolute', top: '0px' }}
            ref={video1Ref}
            onPlay={() => handlePlay(0)}
            onPause={() => handlePause(0)}
            controls >
            <source src={Video} type="video/mp4" />
          </video>
        </div> */}
        <Boxes />
        <div
          style={{ position: "absolute", right: "10px", bottom: "20px" }}
        >
          <ContinueButton
            onClick={handleNextClick}
            className="continue-button"
            role="button"
          >
            <i className="fa fa-play" style={{ marginRight: "5px" }}></i>{" "}
            Next
          </ContinueButton>
        </div>

      </div>
    </div>
  );
}
