import React from "react";
import IntroSrc from "./assets/intro.jpg";
import { ContinueButton } from "./solarWind.styles";

export function Details(props) {
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              <p>
                Designing an MPPT (Maximum Power Point Tracking) controller for
                a solar-wind hybrid system involves creating a circuit that
                optimizes the power output from both solar panels and wind
                turbines by constantly tracking and adjusting for their maximum
                power points. Here&#39;s a simplified overview of the circuit:
              </p>
              <p>
                Basic Circuit Overview: Solar Panel and Wind Turbine Inputs: The
                circuit takes inputs from both the solar panels and wind
                turbines. The MPPT Solar Wind Hybrid Controller: This combines a
                solar charge controller and a wind turbine charge controller,
                allowing you to charge the battery bank using both solar energy
                and wind energy. This Solar and Wind Charge Controller integrate
                MPPT Technology, which allows you to track the most power from
                the sun and wind and get the greatest energy output. MPPT also
                has a 3-stage charging process, unlike PWM, MPPT has the ability
                to automatically switch the charging based on the PV condition.
              </p>
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => props.onStepChange(3)}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Next
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
