import React, { useState } from "react";
import { NavBar } from "../_components/NavBar";
// import { SuccessMessage } from "../_components/SuccessMessage";
// import { FailMessage } from "../_components/FailMessage";
import { IntroPage } from "./intro";
import { Details } from "./details";
import { Practical } from "./practical";
import { Questions } from "./questions";
import { MainConatiner } from "../_components/mainContainer.style";

export function OffShoreWindFarmMainPage(props) {
  const [visibleStep, setVisibleStep] = useState(1);
  const onStepChange = (step) => {
    setVisibleStep(step);
  };
  return (
    <MainConatiner theme={"hydro"}>
      <NavBar onStepChange={onStepChange} />
      {visibleStep == 1 && <IntroPage onStepChange={onStepChange} />}
      {visibleStep == 2 && <Details onStepChange={onStepChange} />}
      {visibleStep == 3 && <Practical onStepChange={onStepChange} />}
      {visibleStep == 4 && <Questions onStepChange={onStepChange} />}
      {/* {visibleStep == 5 && (
        <SuccessMessage
          message='You have achieved the
                learning outcomes of  "Solar Wind Hybrid System"'
          isSuccess={true}
        />
      )}
      {visibleStep == 6 && (
        <FailMessage
          message='You need further understanding theory of
                "Solar Wind Hybrid System"'
          onStepChange={onStepChange}
          isSuccess={false}
        />
      )} */}
    </MainConatiner>
  );
}
