import React from "react";
import { Card, CardContainer, SelectingContainer } from "./practical.styles";
import { styled } from "../../../../stitches.config";

export function SelectMicro(props) {
  return (
    <SelectingContainer>
      <h4 style={{ textAlign: "center", marginBottom: "25px" }}>
        Select pH Value
      </h4>
      <CardContainer style={{ margin: "0 50px" }}>
        <MicroCard
          style={{ backgroundColor: "#ffccbc" }}
          onClick={() => {
            props.setSelectedTMicro("Yeast");
            props.changeCurrentStep();
          }}
        >
          <MicroText>Saccharomyces cerevisiae (Yeast)</MicroText>
        </MicroCard>
        <MicroCard
          style={{ backgroundColor: "#d7ccc8" }}
          onClick={() => {
            props.setSelectedTMicro("PM");
            props.changeCurrentStep();
          }}
        >
          <MicroText>Pseudomonas Mendocino (PM)</MicroText>
        </MicroCard>
      </CardContainer>
    </SelectingContainer>
  );
}

const MicroText = styled("h5", {
  fontSize: "18px",
  textAlign: "center",
  margin: "auto",
});

const MicroCard = styled(Card, {
  display: "flex",
  alignItems: "center",
});
