import React, { useState } from "react";
import { NavBar } from "../_components/NavBar";
import { SuccessMessage } from "../_components/SuccessMessage";
import { FailMessage } from "../_components/FailMessage";
import { BioMassPractical } from "./components/practical/practical";
import { MainConatiner } from "../_components/mainContainer.style";
import { BioMassIntro } from "./components/intro";

export function BioMassMainPage(props) {
  const [visibleStep, setVisibleStep] = useState(2);
  const onStepChange = (step) => {
    setVisibleStep(step);
  };
  return (
    <MainConatiner theme={"bioMass"}>
      <NavBar onStepChange={onStepChange} />
      {visibleStep == 1 && <BioMassIntro onStepChange={onStepChange} />}
      {visibleStep == 2 && <BioMassPractical onStepChange={onStepChange} />}
      {visibleStep == 5 && (
        <SuccessMessage
          message='You have achieved the
                learning outcomes of  "The effect of
                non-normal incidence for the loss of
                effective irradiance"'
          isSuccess={true}
        />
      )}
      {visibleStep == 6 && (
        <FailMessage
          message='You need further understanding theory of
                "The effect of non-normal incidence for the loss of effective irradiance"'
          onStepChange={onStepChange}
          isSuccess={false}
        />
      )}
    </MainConatiner>
  );
}
