import React, { useEffect, useRef, useState } from "react";
import ReactAnimationFrame from "react-animation-frame";
import "./turbine.css";
import TurbineBlades from "./blades";

export function Tower(props) {
  const towerHeight = ((props.towerHeight * 2.2 + 300) * 100) / 200;
  const bladeYOffset = 350 - towerHeight - 600;
  const towerHeightScale = (0.6 * towerHeight) / 350 + 0.4;
  return (
    <>
      <div style={{ position: "relative", top: 520, left: 400, zIndex: 1111 }}>
        <svg className="turbine-tower" overflow="visible" width={1} height={1}>
          <g className="tower">
            <polygon
              className="tower"
              points={`${towerHeightScale * 14},0 -${
                towerHeightScale * 14
              },0 -${towerHeightScale * 5},-${towerHeight} ${
                towerHeightScale * 5
              },-${towerHeight}`}
            />
            <polygon
              className="tower-shadow"
              points={`-1.5,0 -${towerHeightScale * 14},0 -${
                towerHeightScale * 5
              },-${towerHeight} -1,-${towerHeight}`}
            />
          </g>

          <TurbineBlades
            windVelocity={props.speed / 2 + 1.5}
            bladeLength={props.bladeLength / 3 + 60}
            x="0"
            y={bladeYOffset}
            width="200"
          />
        </svg>
      </div>
    </>
  );
}
