import React from "react";
import LoadsSrc from "../assets/load.png";

export function LoadsImage(props) {
  return (
    <>
      <img
        src={LoadsSrc}
        style={{
          position: "absolute",
          top: "165px",
          left: "655px",
          width: "80px",
        }}
      />
    </>
  );
}
