import React, { useState } from "react";
import { ExperimentConatiner } from "../../_components/experimentContainer.styles";
import HydroIntroSrc from "../assets/plant.png";
import { Slider } from "@material-ui/core";
import {
  Chart as ChartJS,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  Legend,
} from "chart.js";
ChartJS.register(LinearScale, PointElement, LineElement, Tooltip, Legend);

export function HydroPlantPractical(props) {
  const [value, setValue] = useState(3);
  const changeValue = (event, value) => {
    setValue(value);
  };
  return (
    <ExperimentConatiner>
      <div
        style={{
          background: "white",
          marginTop: "0px",
          marginLeft: "95px",
          width: "80%",
          height: "80%",
        }}
      >
        <img src={HydroIntroSrc} style={{ width: "100%" }} />
        <div style={{ display: "flex", marginTop: "10px" }}>
          <div
            style={{
              marginBottom: "15px",
              display: "flex",
              flexDirection: "row",
              fontSize: "18px",
              flexBasis: "80%",
            }}
          >
            Flow Rate in m/s : {value}
          </div>
          <div>Output power in W : {value + 10}</div>
        </div>
        <Slider
          size="large"
          style={{ width: 500 }}
          min={0}
          max={180}
          step={1}
          value={value}
          onChange={changeValue}
          valueLabelDisplay="on"
          // getAriaValueText={getText}
          // valueLabelFormat={getText}
          aria-labelledby="discrete-slider-restrict"
          color="secondary"
        />
      </div>
    </ExperimentConatiner>
  );
}
