import React from "react";
import { useState } from "react";
import CircuitSrc from "../assets/main.jpg";

export function SeriesCircuitIntro(props) {
  return (
    <>
      <div>
        <div
          style={{
            width: "1020px",
            position: "absolute",
            border: "solid",
            borderWidth: "0.1rem",
            borderColor: "white",
            left: "10px",
            bottom: "10px",
          }}
        >
          <img src={CircuitSrc} style={{ width: "100%" }} />
        </div>
        <div style={{ position: "absolute", left: "50px", top: "150px" }}>
          <span style={{ color: "black", fontSize: "36px" }}>
            Serial and Parallel battery circuit
            <br></br>
          </span>
          <div
            style={{
              color: "black",
              fontFamily: "Arial",
              marginTop: "10px",
              width: "330px",
              display: "block",
              lineHeight: "24px",
            }}
          >
            Learning Outcome:Demonstrate the number of batteries in series and
            parallel to get the given Voltage, Power and Energy requirements
          </div>
        </div>
        <div style={{ position: "absolute", right: "50px", bottom: "50px" }}>
          <button
            onClick={() => props.onStepChange(2)}
            className="button-solarPV"
            role="button"
          >
            <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Begin
          </button>
        </div>
      </div>
    </>
  );
}
