import { styled } from "../../stitches.config";

export const ArrowCurrent = styled("i", {
  border: "solid red",
  borderWidth: " 0 3px 3px 0",
  display: "inline-block",
  padding: "4px",
  transform: "rotate(-45deg)",
  variants: {
    lineColor: {
      red: {
        borderColor: "red",
      },
      blue: {
        borderColor: "#0096FF",
      },
    },
    direction: {
      up: {
        transform: "rotate(-135deg)",
      },
      down: {
        transform: "rotate(45deg)",
      },
      left: {
        transform: "rotate(135deg)",
      },
    },
  },
});

export const ContinueButton = styled("div", {
  backgroundImage:
    "radial-gradient(100% 100% at 100% 0, rgb(78, 117, 136) 0, rgb(19,65,88) 100%)",
  borderRadius: "999px",
  boxShadow: "#507286 0 10px 20px -10px",
  "&:hover": {
    backgroundImage:
      "radial-gradient(100% 100% at 100% 0,  rgb(19,65,88) 0, rgb(19,65,88) 100%)",
  },
});
