import React, { useState } from "react";
import Draggable from "react-draggable";
import batterystorageSrc from "../assets/batterystorage.png";
import {
  BatteryData,
  WRONGPOSITIONMESSAGE,
  checkWhichPoint,
} from "../constants";

export function Battery(props) {
  const [data, setData] = useState({
    x: 0,
    y: 0,
    correctPoint: false,
  });

  const handleDragController = (e, ui) => {
    setData({
      ...data,
      x: data.x + ui.deltaX,
      y: data.y + ui.deltaY,
    });
  };
  const handleControllerStop = (e, position) => {
    const { x, y } = { ...position };
    const result = checkWhichPoint(x, y, "G");
    if (result) {
      setData({
        x: BatteryData.left,
        y: BatteryData.top,
        correctPoint: true,
      });
    } else {
      alert(WRONGPOSITIONMESSAGE);
    }
  };
  return (
    <>
      <Draggable
        bounds="parent"
        disabled={data.correctPoint}
        defaultPosition={{ x: data.x, y: data.y }}
        position={{ x: data.x, y: data.y }}
        onDrag={(e, ui) => handleDragController(e, ui)}
        onStop={handleControllerStop}
      >
        <div
          style={{
            width: "70px",
            height: "70px",
            background: `url(${batterystorageSrc})`,
            backgroundSize: "contain",
            backgroundRepeat: "no-repeat",
            position: "absolute",
          }}
        ></div>
      </Draggable>
    </>
  );
}
