import React, { useState } from "react";
import IntroSrc from "./assets/main.jpg";
import OneSrc from "./assets/one.JPG";
import TwoSrc from "./assets/two.JPG";
import { ContinueButton } from "./windTurbine.styles";

export function Details(props) {
  const [step, setStep] = useState(1);
  const onClickContinue = () => {
    if (step === 2) {
      props.onStepChange(3);
    } else {
      setStep(step + 1);
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.95",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>

              {step === 1 && <img src={OneSrc} style={{ width: "53%" }} />}
              {step === 2 && <img src={TwoSrc} style={{ width: "40%" }} />}
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => onClickContinue()}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i>
              {step === 1 && <span>Continue</span>}
              {step === 2 && <span>Next</span>}
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
