import React, { useState } from "react";
import IntroSrc from "./assets/intro.webp";
import { ContinueButton } from "./video.styles";

export function Details(props) {
  const [step, setStep] = useState(1);
  const onClickContinue = () => {
    if (step === 1) {
      setStep(2);
    } else {
      props.onStepChange(3);
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              {step === 1 && (<><p>
                Installing a wind turbine is a complex process that involves several key steps to ensure it
                functions efficiently and safely. Below is a general outline of the steps involved in wind
                turbine installation:
              </p>
                <p>
                  Site Selection: The first step is selecting a suitable site for the wind turbine. This involves
                  assessing the wind resource, proximity to electrical grid connections, environmental impact,
                  and local zoning or permitting requirements.
                </p>
                <p>
                  Environmental and Regulatory Approvals: Before proceeding, it&#39;s necessary to obtain all
                  the required environmental clearances and regulatory approvals. This may include impact
                  assessments and consultations with local communities.
                </p>
                <p>
                  Foundation Construction: The foundation of a wind turbine is critical for its stability. The
                  type of foundation (e.g., concrete slab, pile-driven) depends on the soil characteristics and
                  the turbine size. Excavation and foundation construction are significant undertakings that
                  require precise engineering.
                </p>
                <p>
                  Component Manufacturing and Delivery: Wind turbine components (tower, nacelle,
                  blades) are usually manufactured in factories and then transported to the site. The logistics
                  of transporting large turbine components can be challenging and require careful planning.
                </p></>)}

              {step === 2 && (<>
                <p>
                  Assembly and Erection: Once all components are on-site, the turbine assembly begins.
                  The tower is erected first, followed by the nacelle (which houses the generator and gearbox,
                  among other things), and finally, the blades are attached. Cranes are typically used for lifting
                  these components into place.
                </p>
                <p>
                  Electrical Connections: After the physical assembly, the turbine needs to be connected to
                  the electrical grid. This involves installing electrical wiring within the turbine and connecting it
                  to a transformer to match the grid&#39;s voltage.
                </p>
                <p>
                  Testing and Commissioning: Before the turbine can begin operating, it undergoes a series
                  of tests to ensure everything is functioning correctly. This includes mechanical tests,
                  electrical tests, and safety system checks.
                </p>
                <p>
                  Operation and Maintenance: With the turbine operational, ongoing maintenance is critical
                  to ensure its longevity and efficiency. This includes regular inspections, component
                  lubrication, and monitoring for any signs of wear or malfunction.
                </p>
              </>)}
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => onClickContinue()}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i>
              {step === 1 && <h>Continue</h>}
              {step === 2 && <h>Next</h>}
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
