import React from "react";
import { useNavigate } from "react-router-dom";
import HydroIntroSrc from "../pages/hydro/assets/hydro.png";
import HydroHybridMainSrc from "../pages/hydrogenStorage/assets/main.jpg";
import WindTurbineMainSrc from "../pages/windTurbine/assets/main.jpg";
import SolarHeatIntroSrc from "../pages/solarheat/assets/intro2.PNG";
import SolarIntroSrc from "../assets/solarIntro.jpg";
import SolarPVSrc from "../assets/solarpv.jpg";
import BioSrc from "../assets/bio.png";
import PlantSrc from "../assets/hydroplant.png";
import SolarWindSrc from "../assets/solarWind.jpg";
import CircuitSrc from "../pages/circuit2/assets/main.jpg";
import WindTurbineInstallSrc from "../pages/windTurbineInstall/assets/intro.webp";
import OffShoreWindFarmInstallSrc from "../pages/offshoreWindTurbine/assets/intro.webp";
import SolarInstallSrc from "../pages/solarInstall/assets/intro.webp";
import WindPowerCalculateSrc from "../pages/windTurbinePower/assets/main.jpg";
import WasteEnergySrc from "../pages/wasteEnergy/assets/intro.jpg";
import { CardContainerText } from "./main.style";

import "../App.css";

function MainPageCard(props) {
  const navigate = useNavigate();
  return (
    <div className="col-6 col-md-4 col-lg-4" style={{ marginBottom: "4vW" }}>
      <div
        className="card"
        onClick={() => {
          navigate(`/?pratical=${props.urlSrc}`);
        }}
      >
        <img src={props.imageSrc} className="card-main-img" height={"190px"} />
        <div className="card-container">
          <CardContainerText>{props.title}</CardContainerText>
        </div>
      </div>
    </div>
  );
}

export function MainPage(props) {
  return (
    <div
      className="container"
      style={{ padding: "0vW 3vW 3vW 4vW", textAlign: "center" }}
    >
      <h2 style={{ margin: "10px 20px 4vW 20px", color: "#0178b4" }}>
        Practicals
      </h2>
      <div className="row">
        <MainPageCard
          title="Power generated by a wind turbine"
          imageSrc={WindPowerCalculateSrc}
          urlSrc="windTurbinePower"
        />
        <MainPageCard
          title="Waste to Energy Plant Operation"
          imageSrc={WasteEnergySrc}
          urlSrc="wasteToEnergy"
        />
        <MainPageCard
          title="How to build offshore windfarm"
          imageSrc={OffShoreWindFarmInstallSrc}
          urlSrc="offShoreWindFarm"
        />
        <MainPageCard
          title="Wind Turbine Install Procedure"
          imageSrc={WindTurbineInstallSrc}
          urlSrc="windTubineInstall"
        />
        <MainPageCard
          title="How a Solar Farm is Constructed"
          imageSrc={SolarInstallSrc}
          urlSrc="solarInstall"
        />
        <MainPageCard
          title="Identification of components of Wind Turbine"
          imageSrc={WindTurbineMainSrc}
          urlSrc="windTurbine"
        />
        <MainPageCard
          title="Hydrogen Storage System"
          imageSrc={HydroHybridMainSrc}
          urlSrc="hydrogenStorage"
        />
        <MainPageCard
          title="Hydropower Practical"
          imageSrc={HydroIntroSrc}
          urlSrc="hydro"
        />
        <MainPageCard
          title="Lambert Cosine Law"
          imageSrc={SolarIntroSrc}
          urlSrc="solar"
        />
        <MainPageCard
          title="Solar Heater Practical"
          imageSrc={SolarHeatIntroSrc}
          urlSrc="solar2"
        />
        <MainPageCard
          title="Basic Solar Power Circuit"
          imageSrc={SolarPVSrc}
          urlSrc="circuit"
        />
        <MainPageCard
          title="Serial and Parallel battery circuit"
          imageSrc={CircuitSrc}
          urlSrc="circuit2"
        />
        <MainPageCard
          title="Bioethanol Production"
          imageSrc={BioSrc}
          urlSrc="biomass"
        />
        <MainPageCard
          title="Small hydro power plant"
          imageSrc={PlantSrc}
          urlSrc="smallHydro"
        />
        <MainPageCard
          title="Solar Wind Hybrid System"
          imageSrc={SolarWindSrc}
          urlSrc="solarWind"
        />
      </div>
    </div>
  );
}
