import React from "react";
import { Card, CardContainer, SelectingContainer } from "./practical.styles";
import { styled } from "../../../../stitches.config";

export function SelectTemperature(props) {
  return (
    <SelectingContainer>
      <h4 style={{ textAlign: "center", marginBottom: "25px" }}>
        Select Temperature
      </h4>
      <CardContainer>
        <TempCard
          style={{ backgroundColor: "#ef9a9a" }}
          onClick={() => {
            props.setSelectedTemp("29");
            props.changeCurrentStep();
          }}
        >
          <TempText>29°C</TempText>
        </TempCard>
        <TempCard
          style={{ backgroundColor: "#e57373" }}
          onClick={() => {
            props.setSelectedTemp("32");
            props.changeCurrentStep();
          }}
        >
          <TempText>32°C</TempText>
        </TempCard>
        <TempCard
          style={{ backgroundColor: "#ef5350" }}
          onClick={() => {
            props.setSelectedTemp("35");
            props.changeCurrentStep();
          }}
        >
          <TempText>35°C</TempText>
        </TempCard>
      </CardContainer>
    </SelectingContainer>
  );
}

const TempText = styled("h2", {
  textAlign: "center",
  margin: "auto",
});

const TempCard = styled(Card, {
  backgroundColor: "#ef5350",
  display: "flex",
  alignItems: "center",
});
