import React from "react";
import IntroSrc from "./assets/main.jpg";
import { ContinueButton } from "./windTurbine.styles";

export function Details(props) {
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>

              <p>
                Wind turbines are complex machines designed to convert the
                wind&#39;s kinetic energy into electrical energy. Here&#39;s a
                breakdown of the key components that make up a typical wind
                turbine
              </p>
              <p>
                Rotor Blades: These are the large, aerodynamic blades that catch
                the wind&#39;s energy. They&#39;re usually made of composite
                materials to maximize strength and durability while being
                lightweight.
              </p>
              <p>
                Hub: The hub is where the rotor blades are attached. It serves
                as the central point for the blades and connects to the nacelle.
              </p>
              <p>
                Nacelle: Situated on top of the tower, the nacelle houses all
                the key components of the wind turbine, including the gearbox,
                low- and high-speed shafts, generator, controller, and brake.
              </p>
              <p>
                Gearbox: The gearbox is used to increase the rotational speed
                from the slow turning of the rotor blades to a speed high enough
                for the generator to produce electricity efficiently. However,
                some modern turbines use a direct drive technology that
                eliminates the need for a gearbox.
              </p>
              <p>
                Generator: This converts the rotational energy into electrical
                energy. There are various types of generators used, including
                synchronous and asynchronous (induction) generators.
              </p>
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => props.onStepChange(3)}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Next
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
