import React, { useState } from "react";
import IntroSrc from "./assets/intro.jpg";
import { ContinueButton } from "./video.styles";

export function Details(props) {
  const [step, setStep] = useState(1);
  const onClickContinue = () => {
    if (step === 4) {
      props.onStepChange(3);
    }
    else {
      setStep(step + 1);
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              {step === 1 && (
                <>
                  <p>Waste-to-Energy (WtE) plant operation involves converting municipal and industrial
                    waste into electricity and/or heat through various processes. The theory behind WtE
                    plants encompasses several core components, which include waste processing,
                    thermal treatment, energy recovery, and emission control. Here&#39;s an overview of the
                    operation theory behind WtE plants:</p>
                  <br></br>
                  <h5> 1. Waste Processing and Pre-treatment</h5>
                  <p>
                    <span style={{ fontWeight: '600' }}>Waste Collection and Transportation: </span>Waste is collected from various
                    sources, including residential, commercial, and industrial areas, and then
                    transported to the WtE plant.<br></br>
                    <span style={{ fontWeight: '600' }}> Sorting and Pre-treatment: </span>Upon arrival at the plant, waste is sorted to
                    remove recyclables and hazardous materials. The remaining waste, often
                    called the refuse-derived fuel (RDF), is then prepared for thermal treatment.
                    This process may involve shredding, drying, and compacting the waste to
                    improve its calorific value.
                  </p>

                </>
              )}

              {step === 2 && (
                <>
                  <br></br>
                  <h5>2. Thermal Treatment</h5>
                  <p>
                    The core of WtE operations is the thermal treatment of waste, which can be
                    achieved through several methods:<br></br><br></br>
                    <span style={{ fontWeight: '600' }}>Incineration: </span>The most common method, where waste is combusted at high
                    temperatures (850°C to 1,000°C), reducing its volume by up to 90%. The heat
                    generated is used to produce steam.<br></br>
                    <span style={{ fontWeight: '600' }}>Gasification: </span>This process converts organic or fossil fuel-based materials into
                    carbon monoxide, hydrogen, and carbon dioxide by reacting the material at
                    high temperatures (700°C to 1,000°C), with a controlled amount of oxygen
                    and/or steam. The gas produced, known as syngas, can be used to generate
                    electricity and heat.<br></br>
                    <span style={{ fontWeight: '600' }}>Pyrolysis: </span>This is a thermal decomposition of materials at elevated
                    temperatures (450°C to 750°C) in an anaerobic environment. It produces a
                    mixture of solid residue (char), liquid (oil), and gas, which can be used for
                    energy generation.
                  </p>


                </>
              )}
              {step === 3 && (
                <>
                  <br></br>
                  <h5>3. Energy Recovery</h5>
                  <p>
                    <span style={{ fontWeight: '600' }}>Steam Generation: </span>Heat from the combustion process is used to boil water,
                    producing steam. This steam can drive turbines to generate electricity or can
                    be used directly for heating purposes in district heating systems.<br></br>
                    <span style={{ fontWeight: '600' }}> Electricity and Heat Production: </span>The steam drives turbines connected to
                    generators, producing electricity. Excess heat can be used for local heating
                    networks, improving the overall energy efficiency of the plant.<br></br>
                  </p>
                  <br></br>
                  <h5>4. Emission Control and Ash Management</h5>
                  <p>
                    <span style={{ fontWeight: '600' }}>Emission Control:</span> WtE plants are equipped with advanced emission control
                    systems to reduce pollutants such as NOx, SOx, heavy metals, and dioxins.
                    Filters, scrubbers, and catalytic converters are commonly used to clean the
                    flue gases before they are released into the atmosphere.<br></br>
                    <span style={{ fontWeight: '600' }}>Ash Management: </span>The residual ash from the combustion process contains
                    metals and other materials that can be recycled or disposed of in landfills. Fly
                    ash, in particular, needs careful handling due to its potential toxicity.<br></br>
                  </p>
                </>
              )}
              {step === 4 && (
                <>
                  <br></br>
                  <h5>5. Environmental and Economic Considerations</h5>
                  <p>
                    <span style={{ fontWeight: '600' }}>Sustainability:</span> WtE plants contribute to waste reduction, energy production,
                    and the reduction of greenhouse gas emissions by diverting waste from
                    landfills and converting it into energy.<br></br>
                    <span style={{ fontWeight: '600' }}> Economic Aspects:</span> The operation of WtE plants involves significant capital
                    and operational expenses. However, the sale of generated electricity, heat,
                    and recovered materials can provide economic benefits.<br></br><br></br>
                  </p>
                  <p>WtE plants play a crucial role in integrated waste management and energy
                    production strategies. They help in managing the increasing volumes of waste,
                    reducing greenhouse gas emissions, and contributing to sustainable energy
                    generation. However, the efficiency, environmental impact, and economic viability of
                    WtE plants depend on the technology used, waste composition, and local conditions.</p>
                </>
              )}
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => onClickContinue()}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i>
              {step === 4 ? <h>Next</h> : <h>Continue</h>}
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
