import React from "react";
import fuelCellSrc from "../assets/fuelCell.png";
import electrolyzerSrc from "../assets/electrolyzer.png";
import converterSrc from "../assets/converter.png";
import h2Src from "../assets/h2.png";
import pvSrc from "../assets/pv.png";
import batterystorageSrc from "../assets/batterystorage.png";
import inverterSrc from "../assets/inverter.png";

export function SidePanel(props) {
  return (
    <div className="grid-container">
      <div>
        <img
          src={fuelCellSrc}
          height="40px"
          className="margin-left-30px"
          onClick={() => props.onComponentClicked("fuelCell")}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        <span>Fuel Cell</span>
      </div>
      <div>
        <img
          src={electrolyzerSrc}
          height="40px"
          className="margin-left-30px"
          onClick={() => props.onComponentClicked("electrolyzer")}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        <span>Electrolyzer</span>
      </div>
      <div>
        <img
          src={converterSrc}
          height="60px"
          className="margin-left-30px"
          onClick={() => props.onComponentClicked("converter")}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        <span>Converter</span>
      </div>

      <div>
        <img
          src={h2Src}
          height="70px"
          className="margin-left-30px"
          onClick={() => props.onComponentClicked("h2")}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        <span>H2 Cylinder</span>
      </div>

      <div>
        <img
          src={pvSrc}
          height="40px"
          className="margin-left-30px"
          onClick={() => props.onComponentClicked("pv")}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        <span>PV Inverter</span>
      </div>

      <div>
        <img
          src={batterystorageSrc}
          height="50px"
          className="margin-left-30px"
          onClick={() => props.onComponentClicked("battery")}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        <span>Battery storage</span>
      </div>

      <div>
        <img
          src={inverterSrc}
          height="50px"
          className="margin-left-30px"
          onClick={() => props.onComponentClicked("inverter")}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        <span>Inverter</span>
      </div>
    </div>
  );
}
