import React, { useState } from "react";
import IntroSrc from "./assets/intro.webp";
import { ContinueButton } from "./video.styles";

export function Details(props) {
  const [step, setStep] = useState(1);
  const onClickContinue = () => {
    if (step === 5) {
      props.onStepChange(3);
    }
    else {
      setStep(step + 1);
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              {step === 1 && (
                <>
                  <h5> DEVELOPMENT AND PROJECT MANAGEMENT</h5>
                  <h5> Function</h5>
                  <p>

                    Development and project management covers the activities up to the point
                    of financial close or placing firm orders to proceed with wind farm
                    construction. This includes activities required to secure planning consents such
                    as the environmental impact assessment, and activities required to define the
                    design and engineering aspects.
                  </p>
                  <h5> What it costs</h5>
                  <p>
                    About £120 million for a 1GW wind farm. This includes development and
                    consenting services, environmental surveys, resource and metocean
                    assessment, geological and geotechnical surveys, engineering and
                    consultancy. It also includes development expenditure incurred by lost
                    projects (not itemised in sections below).
                  </p>
                  <h5> The teams working within the development phase are,</h5>
                  <p>
                    T.1 Development and consenting services<br></br>
                    T.2 Environmental surveys<br></br>
                    T.3 Resource and metocean assessment<br></br>
                    T.4 Geological and hydrographical surveys<br></br>
                    T.5 Engineering and consultancy<br></br>
                  </p>
                </>
              )}

              {step === 2 && (
                <>
                  <h5> INSTALLATION AND COMMISSIONING</h5>
                  <h5> Function</h5>
                  <p>
                    All installation and commissioning of balance of plant and turbines, including
                    land- and sea-based activity. For offshore activities, the process starts by
                    transporting components from the nearest port to manufacture to either the
                    I.7 Construction port or straight to site. Activities are complete at the wind farm
                    construction works completion date, where assets are handed over to
                    operational teams.
                  </p>
                  <h5> What it costs</h5>
                  <p>
                    About £650 million for a 1GW wind farm. This includes the installation of the
                    balance of plant and turbines, with related offshore logistics. It also includes
                    developer’s insurance, construction project management and spent
                    contingency (not itemised in sections below).
                  </p>
                </>
              )}
              {step === 3 && (
                <>
                  <h5> INSTALLATION AND COMMISSIONING</h5>
                  <h5> Key facts</h5>
                  <p>
                    Today, the typical process for installation is to install the wind farm in the
                    following sequence, with overlaps where possible: The installation period for a
                    1GW wind farm is typically three years from the start of onshore works.
                  </p>
                  <p>
                    1 Foundation installation<br></br>
                    2 Offshore substation installation<br></br>
                    3 Onshore substation construction<br></br>
                    4 Onshore export cable installation<br></br>
                    5 Offshore cable installation<br></br>
                    6 Turbine installation<br></br>
                    7 Construction port<br></br>
                    8 Offshore logistics<br></br>
                  </p>
                </>
              )}
              {step === 4 && (
                <>
                  <h5> OPERATION, MAINTENANCE AND SERVICE</h5>
                  <h5> Function</h5>
                  <p>
                    Operation, maintenance and service (OMS) are the combined functions
                    which, during the lifetime of the wind farm, support the ongoing operation of
                    the wind turbines, balance of plant and associated transmission assets. OMS
                    activities formally start at the wind farm construction works completion date.

                    The focus of these activities during the operational phase is to ensure safe
                    operations, to maintain the physical integrity of the wind farm assets and to
                    optimise electricity generation.
                  </p>
                  <h5> What it costs</h5>
                  <p>
                    About £75 million per annum for a 1GW wind farm. This includes insurance,
                    environmental studies, compensation payments and other internal asset
                    owner costs.
                  </p>
                </>
              )}

              {step === 5 && (
                <>
                  <h5> DECOMMISSIONING</h5>
                  <h5> Function</h5>
                  <p>
                    Removal or making safe of offshore infrastructure at the end of its useful life, plus
                    disposal of equipment.
                  </p>
                  <h5> What it costs</h5>
                  <p>
                    The decommissioning of a 1GW offshore wind farm will cost around £300 million (gross,
                    excluding any resale value of equipment removed).
                  </p>
                  <h5> What's in it</h5>
                  <p>
                    1 Turbine decommissioning<br></br>
                    2 Foundation decommissioning<br></br>
                    3 Cable decommissioning<br></br>
                    4 Substation decommissioning<br></br>
                    5 Decommissioning port<br></br>
                    6 Reuse, recycling or disposal<br></br>
                    7 Environmental surveys<br></br>
                  </p>
                </>
              )}
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => onClickContinue()}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i>
              {step === 5 ? <h>Next</h> : <h>Continue</h>}
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
