import React from 'react';
import HydroIntroSrc from '../assets/intro.jpg';

export function HydroIntro(props) {
    return (
      <div>
        <div
          style={{
            width: "1020px",
            position: "absolute",
            border: "solid",
            borderWidth: "0.1rem",
            borderColor: "white",
            left: "10px",
            bottom: "10px",
          }}
        >
          <img src={HydroIntroSrc} style={{ width: "100%" }} />
        </div>
        <div style={{ position: "absolute", left: "50px", top: "150px" }}>
          <span style={{ color: "black", fontSize: "36px" }}>
            Effect of flow rate on the output power of a small scale hydro power
            plant<br></br>
          </span>
          <div
            style={{
              color: "black",
              fontSize: "20px",
              fontFamily: "Arial",
              marginTop: "10px",
              width: "330px",
              display: "block",
              lineHeight: "24px",
            }}
          >
            Learning Outcome:The output of a hydro power system varies with the
            flow rate according to the equation.
          </div>
        </div>
        <div style={{ position: "absolute", right: "50px", bottom: "50px" }}>
          <button
            onClick={() => props.onStepChange(2)}
            className="button-next button-hydro"
            role="button"
          >
            <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Begin
          </button>
        </div>
      </div>
    );
}


