import React, { useState, useEffect } from "react";
import BananasSrc from "../../assets/bananas.jpg";
import PapayaSrc from "../../assets/papaya.jpg";
import JackFrc from "../../assets/jackfruit.webp";
import RefreshSrc from "../../assets/refresh.png";
import { CardContainer } from "./practical.styles";
import { styled } from "../../../../stitches.config";

export function Selected(props) {
  const [selectedFruit, setSelectedFruit] = useState("");
  const [selectedMicro, setSelectedMicro] = useState("");
  const [microColor, setMicroColor] = useState("");
  const [selectedTemp, setSelectedTemp] = useState("");
  const [tempColor, setTempColor] = useState("");
  const [selectedPH, setSelectedPH] = useState("");
  const [phColor, setPHColor] = useState("");
  const [selectedCon, setSelectedCon] = useState("");
  const [conColor, setConColor] = useState("");

  useEffect(() => {
    if (props.selectedFruit === "Banana") setSelectedFruit(BananasSrc);
    else if (props.selectedFruit === "Papaya") setSelectedFruit(PapayaSrc);
    else if (props.selectedFruit === "Jack") setSelectedFruit(JackFrc);
    else setSelectedFruit("");
  }, [props.selectedFruit]);

  useEffect(() => {
    if (props.selectedMicro === "Yeast") {
      setSelectedMicro("Saccharomyces cerevisiae (Yeast)");
      setMicroColor("#ffccbc");
    } else if (props.selectedMicro === "PM") {
      setSelectedMicro("Pseudomonas Mendocino (PM)");
      setMicroColor("#d7ccc8");
    } else {
      setSelectedMicro("");
      setMicroColor("");
    }
  }, [props.selectedMicro]);

  useEffect(() => {
    if (props.selectedTemp === "29") {
      setSelectedTemp("29°C");
      setTempColor("#ef9a9a");
    } else if (props.selectedTemp === "32") {
      setSelectedTemp("32°C");
      setTempColor("#e57373");
    } else if (props.selectedTemp === "35") {
      setSelectedTemp("35°C");
      setTempColor("#ef5350");
    } else {
      setSelectedTemp("");
      setTempColor("");
    }
  }, [props.selectedTemp]);
  useEffect(() => {
    if (props.selectedPH === "4.3") {
      setSelectedPH("4.3");
      setPHColor("#d4e157");
    } else if (props.selectedPH === "5.0") {
      setSelectedPH("5.0");
      setPHColor("#9ccc65");
    } else if (props.selectedPH === "5.7") {
      setSelectedPH("5.7");
      setPHColor("#7cb342");
    } else {
      setSelectedPH("");
      setPHColor("");
    }
  }, [props.selectedPH]);

  useEffect(() => {
    if (props.selectedCon === "1") {
      setSelectedCon("1:1");
      setConColor("#b0bec5");
    } else if (props.selectedCon === "2") {
      setSelectedCon("1:1.5");
      setConColor("#90a4ae");
    } else if (props.selectedCon === "3") {
      setSelectedCon("1:2");
      setConColor("#78909c");
    } else {
      setSelectedCon("");
      setConColor("");
    }
  }, [props.selectedCon]);

  return (
    <SelectedContainer>
      <h3 style={{ textAlign: "center", margin: "10px 0 25px" }}>
        Fermentation processes parameters
        <img
          src={RefreshSrc}
          width={"30px"}
          style={{ marginLeft: "10px" }}
          onClick={() => props.clickRefresh()}
        />
      </h3>
      <CardContainer>
        <CardWithText>
          <Card>
            <img src={selectedFruit} width={"100%"} />
          </Card>
          <CardText>Fruit</CardText>
        </CardWithText>

        <CardWithText>
          <OtherCard style={{ backgroundColor: microColor }}>
            <MicroText>{selectedMicro}</MicroText>
          </OtherCard>
          <CardText>Microorganism</CardText>
        </CardWithText>

        <CardWithText>
          <OtherCard style={{ backgroundColor: tempColor }}>
            <OtherText>{selectedTemp}</OtherText>
          </OtherCard>
          <CardText>Temperature</CardText>
        </CardWithText>

        <CardWithText>
          <OtherCard style={{ backgroundColor: phColor }}>
            <OtherText>{selectedPH}</OtherText>
          </OtherCard>
          <CardText>pH</CardText>
        </CardWithText>

        <CardWithText>
          <OtherCard style={{ backgroundColor: conColor }}>
            <OtherText>{selectedCon}</OtherText>
          </OtherCard>
          <CardText>Concentration</CardText>
        </CardWithText>
      </CardContainer>
    </SelectedContainer>
  );
}

const Card = styled("div", {
  boxShadow: "0 4px 8px 0 rgba(0,0,0,0.4)",
  transition: "0.3s",
  width: "120px",
  height: "120px",
  borderRadius: "15px",
  "&:hover": {
    boxShadow: "0 8px 16px 0 rgba(0,0,0,0.6)",
  },
  img: {
    borderRadius: "inherit",
  },
});

const SelectedContainer = styled("div", {
  width: "80%",
  position: "absolute",
  marginTop: "20px",
  left: "50%",
  transform: "translateX(-50%)",
});

const CardWithText = styled("div", {
  display: "flex",
  flexDirection: "column",
});

const CardText = styled("p", {
  fontSize: "15px",
  fontWeight: "700",
  textAlign: "center",
});

const OtherCard = styled(Card, {
  display: "flex",
  alignItems: "center",
});

const MicroText = styled("h5", {
  fontSize: "15px",
  textAlign: "center",
  margin: "auto",
});

const OtherText = styled("h4", {
  textAlign: "center",
  margin: "auto",
});
