import React, { useState } from "react";
import SolarPVSrc from "../assets/main.jpg";
import CircuitSrc from "../assets/circuit.webp";

export function SolarPVDetails(props) {
  const [step, setStep] = useState(1);
  const onClickContinue = () => {
    if (step === 1) {
      setStep(2);
    } else {
      props.onStepChange(3);
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={SolarPVSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.9",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              {step === 1 && (
                <div style={{ display: "flex", justifyContent: "center" }}>
                  <img
                    src={CircuitSrc}
                    style={{ height: "390px", display: "block" }}
                  />
                </div>
              )}
              {step === 2 && (
                <div>
                  <div>
                    a) The voltage of a series assembly of batteries can be
                    calculated using the following equation: V = V1 + V2 + V3 +
                    ... + Vn Where: • V is the total voltage of the series
                    assembly • V1, V2, V3, ..., Vn are the voltages of the
                    individual batteries in the assembly{" "}
                  </div>
                  <div>
                    b) The current capacity of the assembly remains the same as
                    that of a single battery.
                  </div>
                  <div>
                    c) The capacity of a series assembly of batteries remains
                    the same as that of a single battery (C_total = C1)
                  </div>
                  <div>
                    d) The voltage of a parallel assembly of batteries is the
                    same as that of a single battery V = (V1 = V2 = V3 = ... =
                    Vn)
                  </div>
                  <div>
                    e) The capacity of the assembly is the sum of the capacities
                    of the individual batteries C = (C1 + C2 + C3 + ... + Cn)
                  </div>
                  <div style={{marginTop:'10px',fontWeight:'600'}}>
                    Note: It's important to note that when connecting
                    batteries in parallel, they should have the same voltage
                    rating, but the same capacity is not necessary. However, it
                    is recommended to use batteries with similar capacities to
                    avoid imbalances in the assembly, which can cause some
                    batteries to discharge faster than others.
                  </div>
                </div>
              )}
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <button
              onClick={() => onClickContinue()}
              className="button-solarPV"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i>{" "}
              {step === 1 && <h>Continue</h>}
              {step === 2 && <h>Next</h>}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
