import React from "react";
import SolarSrc from "../assets/solar.png";

export function SolarImage(props) {
  return (
    <>
      <img
        src={SolarSrc}
        style={{
          position: "absolute",
          top: "85px",
          left: "20px",
          width: "150px",
        }}
      />
    </>
  );
}
