import React, { useState } from 'react';
import { HydroDetails } from './components/details';
import { HydroIntro } from './components/intro';
import { HydroPractical } from './components/practical';
import { Questions } from './components/questions';
import './hydro.css';
import '../../App.css';
import { SuccessMessage } from './components/successMessage';
import { FailMessage } from './components/failMessage';
import { NavBar } from '../_components/NavBar';
import { MainConatiner } from '../_components/mainContainer.style';

export function HydroMainPage(props) {
    const [visibleStep, setVisibleStep] = useState(1);
    const onStepChange = (step) => {
        setVisibleStep(step);
    }
    return (
      <MainConatiner theme={"hydro"}>
        <NavBar onStepChange={onStepChange} />
        {visibleStep == 1 && <HydroIntro onStepChange={onStepChange} />}
        {visibleStep == 2 && <HydroDetails onStepChange={onStepChange} />}
        {visibleStep == 3 && <HydroPractical onStepChange={onStepChange} />}
        {visibleStep == 4 && <Questions onStepChange={onStepChange} />}
        {visibleStep == 5 && <SuccessMessage />}
        {visibleStep == 6 && <FailMessage onStepChange={onStepChange} />}
      </MainConatiner>
    );
}


