import React, { useState } from "react";
import { ExperimentConatiner } from "../../../_components/experimentContainer.styles";
import { SelectFruit } from "./SelectFruit";
import { BottomContainer, TopContainer } from "./practical.styles";
import { SelectTemperature } from "./SelectTemperature";
import { SelectPH } from "./SelectPH";
import { SelectMicro } from "./SelectMicro";
import { Selected } from "./Selected";
import { SelectConcentration } from "./SelectConcentration";

export function BioMassPractical(props) {
  const [currentStep, setCurrentStep] = useState(0);
  const [selectedFruit, setSelectedFruit] = useState("");
  const [selectedMicro, setSelectedTMicro] = useState("");
  const [selectedTemp, setSelectedTemp] = useState("");
  const [selectedPH, setSelectedPH] = useState("");
  const [selectedCon, setSelectedCon] = useState("");

  const changeCurrentStep = () => {
    if (currentStep < 4) setCurrentStep(currentStep + 1);
  };

  const clickRefresh = () => {
    setCurrentStep(0);
    setSelectedFruit("");
    setSelectedTMicro("");
    setSelectedTemp("");
    setSelectedPH("");
    setSelectedCon("");
  };
  return (
    <ExperimentConatiner>
      <TopContainer>
        {currentStep === 0 && (
          <SelectFruit
            changeCurrentStep={changeCurrentStep}
            setSelectedFruit={setSelectedFruit}
          />
        )}
        {currentStep === 1 && (
          <SelectMicro
            changeCurrentStep={changeCurrentStep}
            setSelectedTMicro={setSelectedTMicro}
          />
        )}
        {currentStep === 2 && (
          <SelectTemperature
            changeCurrentStep={changeCurrentStep}
            setSelectedTemp={setSelectedTemp}
          />
        )}
        {currentStep === 3 && (
          <SelectPH
            changeCurrentStep={changeCurrentStep}
            setSelectedPH={setSelectedPH}
          />
        )}
        {currentStep === 4 && (
          <SelectConcentration
            changeCurrentStep={changeCurrentStep}
            setSelectedCon={setSelectedCon}
          />
        )}
      </TopContainer>

      <BottomContainer>
        <Selected
          clickRefresh={clickRefresh}
          selectedFruit={selectedFruit}
          selectedMicro={selectedMicro}
          selectedTemp={selectedTemp}
          selectedPH={selectedPH}
          selectedCon={selectedCon}
        />
      </BottomContainer>
    </ExperimentConatiner>
  );
}
