import React, { useEffect, useState } from 'react';
import LogoImgSrc from "../../assets/logo.png";
import PanelImgSrc from "../../assets/solarPractical.png";
import { SolarDetails } from './components/details';
import { SolarIntro } from './components/intro';
import { Slider } from '@material-ui/core';
import '../../App.css';
import './solar.css';

import {
    Chart as ChartJS,
    LinearScale,
    PointElement,
    LineElement,
    Tooltip,
    Legend,
} from 'chart.js';
import { Scatter } from 'react-chartjs-2';
import { Questions } from './components/questions';
import { useNavigate } from 'react-router-dom';
import { NavBar } from '../_components/NavBar';
import { SuccessMessage } from '../_components/SuccessMessage';
import { FailMessage } from '../_components/FailMessage';
ChartJS.register(LinearScale, PointElement, LineElement, Tooltip, Legend);

export const options = {
    plugins: {
        legend: {
            display: false
        }
    },
    scales: {
        xAxes: {
            display: true,
            min: 0,
            max: 180,
            title: {
                display: true,
                text: 'AOI [deg]'
            },
            ticks: {
                callback: function (value, index, values) {
                    return value;
                },
                maxTicksLimit: 430,
                stepSize: 30
            }
        },
        yAxes: {
            title: {
                display: true,
                text: 'Relative Irradiance [a.u]'
            },
        }
    },
};

const marks = [
    {
        value: 0,
        label: '0°',
    },
    {
        value: 15,
        label: '15°',
    },
    {
        value: 30,
        label: '30°',
    },
    {
        value: 45,
        label: '45°',
    },
    {
        value: 60,
        label: '60°',
    },
    {
        value: 75,
        label: '75°',
    },
    {
        value: 90,
        label: '90°',
    },
    {
        value: 105,
        label: '105°',
    },
    {
        value: 120,
        label: '120°',
    },
    {
        value: 135,
        label: '135°',
    },
    {
        value: 150,
        label: '150°',
    },
    {
        value: 165,
        label: '165°',
    },
    {
        value: 180,
        label: '180°',
    },
];


function polarToCartesian(centerX, centerY, radius, angleInDegrees) {
    var angleInRadians = (angleInDegrees - 90) * Math.PI / 180.0;

    return {
        x: centerX + (radius * Math.cos(angleInRadians)),
        y: centerY + (radius * Math.sin(angleInRadians))
    };
}

function describeArc(x, y, radius, startAngle, endAngle) {

    var start = polarToCartesian(x, y, radius, endAngle);
    var end = polarToCartesian(x, y, radius, startAngle);

    var largeArcFlag = endAngle - startAngle <= 180 ? "0" : "1";

    var d = [
        "M", start.x, start.y,
        "A", radius, radius, 0, largeArcFlag, 0, end.x, end.y
    ].join(" ");

    return d;
}

export function SolarMainPage(props) {
    const [visibleStep, setVisibleStep] = useState(1);
    const [value, setValue] = useState(45);
    const [x, setX] = useState(112);
    const [y, setY] = useState(310);
    const [x2, setX2] = useState(0);
    const [y2, setY2] = useState(0);
    const [x3, setX3] = useState(0);
    const [y3, setY3] = useState(0);
    const [chartData, setChartData] = useState({
        datasets: [
            {
                label: '',
                data: [],
                backgroundColor: 'rgba(255, 99, 132, 1)',
            },
        ],
    })
    useEffect(() => {
        setY2(310 - 15 * Math.sin((45) * Math.PI / 180));
        setX2(192 - 15 * Math.cos((45) * Math.PI / 180));

        setY3(310 + 15 * Math.sin((45) * Math.PI / 180));
        setX3(192 - 15 * Math.cos((45) * Math.PI / 180));

    }, []);

    const onStepChange = (step) => {
        setVisibleStep(step);
        setX(112);
        setY(310);
        setY2(310 - 15 * Math.sin((45) * Math.PI / 180));
        setX2(192 - 15 * Math.cos((45) * Math.PI / 180));

        setY3(310 + 15 * Math.sin((45) * Math.PI / 180));
        setX3(192 - 15 * Math.cos((45) * Math.PI / 180));
        setChartData({
            datasets: [
                {
                    label: '',
                    data: [],
                    backgroundColor: 'rgba(255, 99, 132, 1)',
                },
            ],
        });
        setValue(45)
    }

    useEffect(() => {
        if (visibleStep == 3) {
            document.getElementById("arc1").setAttribute("d", describeArc(272, 310, 40, 180, 270));
        }
    }, [visibleStep]);

    const changeValue = (event, value) => {
        setValue(value);
        let tempX = (112 + (160 - Math.cos((value - 45) * Math.PI / 180) * 160));
        let tempy = (310 - Math.sin((value - 45) * Math.PI / 180) * 160);
        setX(tempX);
        setY(tempy);
        //
        setY2(310 - (310 - tempy) / 2 - 15 * Math.sin((value) * Math.PI / 180));
        setX2(tempX + (272 - tempX) / 2 - 15 * Math.cos((value) * Math.PI / 180));
        // 
        setY3(310 - (310 - tempy) / 2 + 15 * Math.sin((90 - value) * Math.PI / 180));
        setX3(tempX + (272 - tempX) / 2 - 15 * Math.cos((90 - value) * Math.PI / 180));
        //
        document.getElementById("arc1").setAttribute("d", describeArc(272, 310, 40, 180, 225 + value));

        setChartData({
            datasets: [
                {
                    label: '',
                    data: [...chartData.datasets[0].data, { x: value, y: Math.sin((value) * Math.PI / 180) }],
                    backgroundColor: 'rgba(255, 99, 132, 1)',
                },
            ],
        })
    }
    const getText = (valu) => `${value}°`;
    return (
        <div className='app-main-tile theme-solar' >
            <NavBar onStepChange={onStepChange} />

            {visibleStep == 1 &&
                <SolarIntro onStepChange={onStepChange} />
            }
            {visibleStep == 2 &&
                <SolarDetails onStepChange={onStepChange} />
            }
            {visibleStep == 3 &&
                <div>
                    <div style={{ width: '1020px', height: '82%', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                        <div style={{ display: 'flex', flexDirection: "column", height: '100%', width: '100%' }}>
                            <div style={{ flex: 12 }} >
                                <div style={{ display: 'flex', flexDirection: "row", height: '100%', width: '100%' }}>
                                    <div style={{ flex: 3 }}>
                                        <div style={{ width: '100%', height: '100%', background: '#29b6f6', position: 'relative' }}>
                                            <svg width="538" height="430" >
                                                <line x1={x} y1={y} x2="272" y2="310" style={{ stroke: '#efea0a', strokeWidth: '2' }} />
                                                <line x1={x2} y1={y2} x2={x + (272 - x) / 2} y2={310 - (310 - y) / 2} style={{ stroke: '#efea0a', strokeWidth: '2' }} />
                                                <line x1={x3} y1={y3} x2={x + (272 - x) / 2} y2={310 - (310 - y) / 2} style={{ stroke: '#efea0a', strokeWidth: '2' }} />
                                                <path id="arc1" fill="none" stroke="#446688" strokeWidth="3" />
                                                <line x1="160" y1="198" x2="272" y2="310" strokeDasharray="5, 4" style={{ stroke: 'rgb(255,0,0)', strokeWidth: '2' }} />
                                                <image x="148" y="170" height="268" xlinkHref={PanelImgSrc} />
                                            </svg>
                                        </div>
                                    </div>
                                    <div style={{ flex: 3, backgroundColor: "white" }} >

                                        <div
                                            style={{ position: 'relative', width: '100%', height: '100%', paddingTop: '20px' }}
                                        >
                                            <Scatter options={options} data={chartData} height={300} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div style={{ flex: 3, background: "#cfd8dc" }} >
                                <div style={{ display: 'flex', flexDirection: "row", height: '100%', width: '100%' }} >
                                    <div style={{ flex: 5 }} >
                                        <div style={{ position: 'relative', top: '10px', left: '110px' }}>
                                            <Slider
                                                size="large"
                                                style={{ width: 500 }}
                                                min={0}
                                                max={180}
                                                step={15}
                                                value={value}
                                                onChange={changeValue}
                                                getAriaValueText={getText}
                                                valueLabelFormat={getText}
                                                aria-labelledby="discrete-slider-restrict"
                                                marks={marks}
                                            />

                                        </div>
                                    </div>
                                    <div style={{ flex: 2, background: '#cfd8dc', }} className="center" >
                                        <button onClick={() => onStepChange(4)} className="button-34" style={{ marginLeft: '100px' }}><i className="fa fa-play" style={{ marginRight: '5px' }} ></i>  Next</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }
            {visibleStep == 4 &&
                <Questions onStepChange={onStepChange} />
            }
            {visibleStep == 5 &&
                <SuccessMessage
                    message='You have achieved the
            learning outcomes of  "The effect of
            non-normal incidence for the loss of
            effective irradiance"' isSuccess={true} />
            }
            {visibleStep == 6 &&
                <FailMessage message='You need further understanding theory of
                "The effect of non-normal incidence for the loss of effective irradiance"'
                    onStepChange={onStepChange}
                    isSuccess={false} />
            }
        </div >
    );
}


