import React, { useEffect, useRef, useState } from "react";
import './turbine.css'

export function Background(props) {
  const [hillsYOffset,sethillsYOffset]=useState(0)
  const [mountainNearYOffset,setmountainNearYOffset]=useState(0)
  const [mountainFarYOffset,setmountainFarYOffset]=useState(0)
  
  useEffect(() => {
    const offset = 150 * props.altitude / 5000;
    const hillsYOffset = offset * 1.25;
    const mountainNearYOffset = offset * 0.95;
    const mountainFarYOffset = offset * 0.6;
    sethillsYOffset(hillsYOffset)
    setmountainNearYOffset(mountainNearYOffset)
    setmountainFarYOffset(mountainFarYOffset)
  }, [props.altitude]);
  return (
    <div style={{position:'relative' , top:4, left:0 ,zIndex:0}}>
    <svg className="background" viewBox="0 0 771 565" preserveAspectRatio="xMinYMin meet" overflow="visible" width="771">
        <defs>
            <linearGradient id="sky-gradient" x1="0%" y1="0%" x2="0%" y2="100%">
                <stop offset="0%" stopColor="#bfe4ff" />
                <stop offset="100%" stopColor="#83caff" />
            </linearGradient>
            <linearGradient id="mountain-far-gradient" x1="0%" y1="0%" x2="0%" y2="100%">
                <stop offset="0%" stopColor="#6d4c41" />
                <stop offset="100%" stopColor="#4e342e" />
            </linearGradient>
            <linearGradient id="mountain-near-gradient" x1="0%" y1="0%" x2="0%" y2="100%">
                <stop offset="0%" stopColor="#8d6e63" />
                <stop offset="100%" stopColor="#5d4037" />
            </linearGradient>
            <linearGradient id="hills-gradient" x1="0%" y1="0%" x2="0%" y2="100%">
                <stop offset="0%" stopColor="#1b5e20" />
                <stop offset="100%" stopColor="#004d40" />
            </linearGradient>
        </defs>
        <g id="background-layer">
            <rect id="sky"  className="sky" width="771" height="565" fill="url(#sky-gradient)" />
            <polygon id="mountain-far" transform={`translate(0 ${mountainFarYOffset})`}  points="771 207.09 651.68 173.63 625.96 142.8 579.79 115.75 555.84 89.99 511.82 127.54 467.9 154.71 426.23 157.56 385.98 173.82 337.38 136.97 301.56 100.26 256.03 132.55 211.27 118.7 166.5 124.86 120.49 118.1 78.12 92.1 -0.5 90.53 -0.5 565.53 771 565.53 771 207.09" fill="url(#mountain-far-gradient)" />
            <polygon id="mountain-near" transform={`translate(0 ${mountainNearYOffset})`} points="771 196.42 625.96 198.98 512.43 218.95 414.17 182.58 335.35 192.48 308.47 157.57 284.19 128.59 196.71 94.77 117.69 146.61 40.98 170.59 -0.5 181.5 -0.5 565.53 771 565.53 771 196.42" fill="url(#mountain-near-gradient)" />
            <path id="hills" transform={`translate(0 ${hillsYOffset})`} d="M771,565H-0.5V269c32.72,0.94,61.38,2.55,92.16,4.91,29.57,2.76,51.07-1.76,82.47,0.62,53.6,3.11,109.81-0.63,163.39-7.38,16.17-0.69,22.67-1.3,37.98-1.08,15.28,0.23,29.19,1.28,45.18,1.49,30.33,0.39,59.79-1.74,88.86-1.41,19.58,0.22,43.81,2.62,64.67,3.43,28.14,1,53.43-5.58,79.53-4.51,17.53,0.84,39.15,6.01,57.92,7.85,25.19,2.8,47.88-1.86,71.54-6.31Z" fill="url(#hills-gradient)" />

        </g>
    </svg>
  </div>
  );
}
