export const WRONGPOSITIONMESSAGE = "Move to correct position in the circuit";
export const checkWhichPoint = (x, y, point) => {
  let result = false;
  console.log(x, y, point);
  if (123 <= y && y <= 197 && 274 <= x && x <= 342) {
    // Point A
    if (point == "A") {
      result = true;
    }
  } else if (93 <= y && y <= 200 && 475 <= x && x <= 528) {
    if (point == "B") {
      result = true;
    }
  } else if (265 <= y && y <= 363 && 478 <= x && x <= 528) {
    if (point == "C") {
      result = true;
    }
  } else if (426 <= y && y <= 459 && 424 <= x && x <= 528) {
    if (point == "D") {
      result = true;
    }
  } else if (320 <= y && y <= 411 && 250 <= x && x <= 343) {
    if (point == "E") {
      result = true;
    }
  } else if (298 <= y && y <= 364 && 32 <= x && x <= 98) {
    if (point == "F") {
      result = true;
    }
  } else if (-1 <= y && y <= 51 && 475 <= x && x <= 526) {
    if (point == "G") {
      result = true;
    }
  }
  return result;
};
