import React from 'react';
import IntroSrc from '../assets/intro2.PNG';

export function SolarHeatIntro(props) {
    return (
        <div>
            <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                <img src={IntroSrc} style={{ width: '100%' }} />
            </div>
            <div style={{ position: 'absolute', left: '50px', top: '150px' }}>
                <span style={{ color: 'white', fontSize: '36px' }}>Solar Thermal and Water Pumping Systems<br></br></span>
                <div style={{ color: 'white', fontFamily: 'Arial', marginTop: '10px', width: '330px', display: 'block', lineHeight: '24px' }}>
                    Learning Outcome: Performance of solar thermal flat plate collector based water heating system.
                </div>
            </div>
            <div style={{ position: 'absolute', right: '50px', bottom: '50px' }}>
                <button onClick={() => props.onStepChange(2)} className="button-solarHeat" role="button"><i className="fa fa-play" style={{ marginRight: '5px' }} ></i>  Begin</button>
            </div>
        </div>
    );
}


