import React from 'react';
import SolarDetailsSrc from "../../../assets/solarIntro.jpg";
import LabGoalSrc from "../../../assets/labgoal.png";

export function SolarDetails(props) {
  return (
    <div>
      <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
        <img src={SolarDetailsSrc} style={{ width: '100%' }} />
        <div style={{
          position: 'absolute', top: '5%', left: '10%', border: 'solid',
          borderWidth: '3px', borderRadius: '10px', borderColor: 'white', width: '80%', height: '90%'
        }}>
          <div style={{ width: '100%', height: '100%', backgroundColor: 'white', opacity: '0.88', position: 'absolute' }}>
            <div style={{ padding: '0px 20px' }} >
              <h2>Theory</h2>
              <p style={{ fontFamily: 'Arial', marginTop: '10px', display: 'block', lineHeight: '20px' }}>
                There is a loss of irradiance due to geometry and reflection when a PV device is not positioned normally to the sun. The Lambert Cosine Law can be used to calculate the loss of effective irradiance due to geometry.</p>
              {/* <h2>Lab Goals</h2> */}
              <img src={LabGoalSrc} style={{ height: '230px' ,display:'block'}} />
              <div style={{ position: 'absolute', left: '42%',paddingTop:'20px' }}>
                <button onClick={() => props.onStepChange(3)} className="button-34" role="button"><i className="fa fa-play" style={{ marginRight: '5px' }} ></i>  Next</button>
              </div>
            </div>

          </div>
        </div>
      </div>

    </div>
  );
}


