import React, { useEffect, useState } from "react";
import turbineSrc from "./assets/turbine.png";
import Draggable from "react-draggable";
import { Points } from "./points";
import { ContinueButton } from "./windTurbine.styles";

export function Practical(props) {
  const [titles, setTitles] = useState([
    {
      id: 5,
      x: 1,
      y: -1,
      show: false,
      correctX: 86,
      correctX1: 73,
      correctX2: 117,
      correctY: 144,
      correctY1: 127,
      correctY2: 164,
      correctPoint: false,
      title: "Rotor",
    },
    {
      id: 2,
      x: 1,
      y: -1,
      show: false,
      correctX: 183,
      correctX1: 162,
      correctX2: 204,
      correctY: 41,
      correctY1: 27,
      correctY2: 63,
      correctPoint: false,
      title: "Pitch",
    },
    {
      id: 6,
      x: 1,
      y: -1,
      show: false,
      correctX: 250,
      correctX1: 195,
      correctX2: 303,
      correctY: 112,
      correctY1: 99,
      correctY2: 135,
      correctPoint: false,
      title: "Low-Speed Shaft",
    },
    {
      id: 1,
      x: 1,
      y: -1,
      show: false,
      correctX: 333,
      correctX1: 303,
      correctX2: 366,
      correctY: 139,
      correctY1: 117,
      correctY2: 154,
      correctPoint: false,
      title: "Gear Box",
    },
    {
      id: 7,
      x: 1,
      y: -1,
      show: false,
      correctX: 390,
      correctX1: 355,
      correctX2: 424,
      correctY: 178,
      correctY1: 162,
      correctY2: 198,
      correctPoint: false,
      title: "Generator",
    },
    {
      id: 14,
      x: 1,
      y: -1,
      show: false,
      correctX: 448,
      correctX1: 413,
      correctX2: 482,
      correctY: 232,
      correctY1: 214,
      correctY2: 250,
      correctPoint: false,
      title: "Controller",
    },
    {
      id: 4,
      x: 1,
      y: -1,
      show: false,
      correctX: 498,
      correctX1: 456,
      correctX2: 542,
      correctY: 203,
      correctY1: 190,
      correctY2: 226,
      correctPoint: false,
      title: "Anemometer",
    },
    {
      id: 8,
      x: 1,
      y: -1,
      show: false,
      correctX: 558,
      correctX1: 521,
      correctX2: 593,
      correctY: 380,
      correctY1: 361,
      correctY2: 397,
      correctPoint: false,
      title: "Wind Vane",
    },
    {
      id: 13,
      x: 1,
      y: -1,
      show: false,
      correctX: 539,
      correctX1: 494,
      correctX2: 545,
      correctY: 414,
      correctY1: 394,
      correctY2: 430,
      correctPoint: false,
      title: "Nacelle",
    },
    {
      id: 15,
      x: 1,
      y: -1,
      show: false,
      correctX: 394,
      correctX1: 341,
      correctX2: 452,
      correctY: 420,
      correctY1: 398,
      correctY2: 434,
      correctPoint: false,
      title: "High-speed Shaft",
    },
    {
      id: 11,
      x: 1,
      y: -1,
      show: false,
      correctX: 190,
      correctX1: 170,
      correctX2: 216,
      correctY: 463,
      correctY1: 446,
      correctY2: 482,
      correctPoint: false,
      title: "Tower",
    },
    {
      id: 9,
      x: 1,
      y: -1,
      show: false,
      correctX: 149,
      correctX1: 115,
      correctX2: 169,
      correctY: 488,
      correctY1: 466,
      correctY2: 502,
      correctPoint: false,
      title: "Blades",
    },
    {
      id: 3,
      x: 1,
      y: -1,
      show: false,
      correctX: 174,
      correctX1: 149,
      correctX2: 195,
      correctY: 254,
      correctY1: 236,
      correctY2: 272,
      correctPoint: false,
      title: "Brake",
    },
    {
      id: 10,
      x: 1,
      y: -1,
      show: false,
      correctX: 193,
      correctX1: 174,
      correctX2: 243,
      correctY: 356,
      correctY1: 340,
      correctY2: 376,
      correctPoint: false,
      title: "Yaw Drive",
    },
    {
      id: 12,
      x: 1,
      y: -1,
      show: false,
      correctX: 173,
      correctX1: 151,
      correctX2: 223,
      correctY: 416,
      correctY1: 398,
      correctY2: 434,
      correctPoint: false,
      title: "Yaw Motor",
    },
  ]);
  const handleDragTitle = (e, ui, index) => {
    const tempTitles = [...titles];
    tempTitles[index].x = tempTitles[index].x + ui.deltaX;
    tempTitles[index].y = tempTitles[index].y + ui.deltaY;
    setTitles([...tempTitles]);
  };
  const onTitleClick = (index) => {
    const tempTitles = [...titles];
    tempTitles[index].show = true;
    setTitles([...tempTitles]);
  };
  const handleTitleStop = (e, position, index) => {
    const { x, y } = { ...position };
    const tempTitles = [...titles];
    // console.log(x, y);
    if (
      tempTitles[index].correctY1 <= y &&
      y <= tempTitles[index].correctY2 &&
      tempTitles[index].correctX1 <= x &&
      x <= tempTitles[index].correctX2
    ) {
      tempTitles[index].x = tempTitles[index].correctX;
      tempTitles[index].y = tempTitles[index].correctY;
      tempTitles[index].correctPoint = true;
      setTitles([...tempTitles]);
    } else {
      alert("Select correct point");
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          height: "82%",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            height: "100%",
            width: "100%",
          }}
        >
          <div
            style={{
              flex: 19,
              border: "solid",
              borderWidth: "1px",
              background: "#ffffff",
              paddingTop: "15px",
            }}
          >
            <div
              style={{
                height: "100%",
                width: "700px",
                position: "absolute",
                top: `60px`,
                left: `60px`,
              }}
            >
              <img src={turbineSrc} width="650px" />
            </div>
            <Points />
            {titles.map((title, index) => (
              <>
                {title.show && (
                  <Draggable
                    key={title.id}
                    bounds="parent"
                    disabled={title.correctPoint}
                    defaultPosition={{ x: title.x, y: title.y }}
                    position={{ x: title.x, y: title.y }}
                    onDrag={(e, ui) => handleDragTitle(e, ui, index)}
                    onStop={(e, ui) => handleTitleStop(e, ui, index)}
                  >
                    <div
                      style={{
                        cursor: "pointer",
                        padding: "0px 3px",
                        background: `black`,
                        backgroundSize: "contain",
                        backgroundRepeat: "no-repeat",
                        position: "absolute",
                        color: "white",
                      }}
                    >
                      {title.title}
                    </div>
                  </Draggable>
                )}
              </>
            ))}
            <div
              style={{ position: "absolute", right: "270px", bottom: "20px" }}
            >
              <ContinueButton
                onClick={() => props.onStepChange(4)}
                className="continue-button"
                role="button"
              >
                <i className="fa fa-play" style={{ marginRight: "5px" }}></i>{" "}
                Next
              </ContinueButton>
            </div>
          </div>

          <div
            style={{
              flex: 6,
              backgroundColor: "#A9A9A9",
              paddingTop: "20px",
            }}
          >
            <div style={{ marginTop: "5px", marginLeft: "10px" }}>
              <label>Select the name of the component</label>
              <div style={{ marginTop: "15px", marginLeft: "50px" }}>
                {titles
                  .sort((a, b) => a.id - b.id)
                  .map((title, index) => (
                    <div
                      onClick={() => onTitleClick(index)}
                      style={{
                        textAlign: "center",
                        marginBottom: "7px",
                        width: "60%",
                        cursor: "pointer",
                        padding: "0px 3px",
                        background: `black`,
                        backgroundSize: "contain",
                        backgroundRepeat: "no-repeat",
                        color: "white",
                      }}
                    >
                      {title.title}
                    </div>
                  ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
