import React from "react";
import IntroSrc from "./assets/main.jpg";
import { ContinueButton } from "./hydrogenStorage.styles";

export function Details(props) {
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              <p>
                The primary concept behind the Hydrogen Storage System involves
                capturing solar-derived electrical energy and storing it as
                hydrogen gas in a dedicated tank. This stored hydrogen serves as
                a fuel source to generate electricity, particularly when solar
                energy isn&#39;t available, ensuring a continuous power supply.
              </p>
              <p>
                To enable this process, the Hydrogen Storage System interfaces
                with Solar Electric Systems comprising solar panels equipped
                with solar cells and an inverter. These components facilitate
                the conversion of sunlight into electricity during daylight
                hours. At night or when solar energy isn&#39;t accessible, the
                stored hydrogen, mixed with air, acts as a fuel source. This
                mixture is utilized to generate electricity, received by an
                inverter that transforms the current for integration into the
                home&#39;s electrical system. This compensates for the absence
                of solar electricity production during nighttime.
              </p>
              <p>
                Moreover, linking the Hydrogen Storage System to Battery Storage
                Systems aids in maintaining a household&#39;s energy
                equilibrium. This entire process is entirely safe and
                environmentally benign, producing oxygen and water as
                byproducts, with minimal to negligible impact on the
                environment.
              </p>
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => props.onStepChange(3)}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Next
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
