import React, { useEffect, useState } from "react";
import { WireD } from "./components/wired";
import { Points } from "./components/points";
import { WireC } from "./components/wirec";
import { WireB } from "./components/wireb";
import { Images } from "./components/images";
import { WireA } from "./components/wirea";
import { WireE } from "./components/wiree";
import { WireF } from "./components/wiref";
import { WireG } from "./components/wireg";
import { WireH } from "./components/wireh";
import { WireI } from "./components/wirei";
import { BatteryWire } from "./components/batteryWire";
import { WireJ } from "./components/wirej";
import { WireK } from "./components/wirek";
import { WireL } from "./components/wirel";
import { TableData } from "./components/tabeldata";
import { ContinueButton, MpptTable } from "./solarWind.styles";
import { NumberPoints } from "./components/numberPoints";

export function Practical(props) {
  const [pointA, setPointA] = useState("Select");
  const [pointB, setPointB] = useState("Select");
  const [pointC, setPointC] = useState("Select");
  const [pointD, setPointD] = useState("Select");
  const [pointE, setPointE] = useState("Select");
  const [pointF, setPointF] = useState("Select");
  const [pointG, setPointG] = useState("Select");
  const [pointH, setPointH] = useState("Select");
  const [pointI, setPointI] = useState("Select");
  const [pointJ, setPointJ] = useState("Select");
  const [pointK, setPointK] = useState("Select");
  const [pointL, setPointL] = useState("Select");

  const onChangePointA = (value) => {
    if (value == "3") {
      setPointA(value);
    } else {
      alert("Select correct point");
    }
  };

  const onChangePointB = (value) => {
    if (value == "1") {
      setPointB(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointC = (value) => {
    if (value == "1") {
      setPointC(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointD = (value) => {
    if (value == "1") {
      setPointD(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointE = (value) => {
    if (value == "2") {
      setPointE(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointF = (value) => {
    if (value == "4") {
      setPointF(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointG = (value) => {
    if (value == "5") {
      setPointG(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointH = (value) => {
    if (value == "10") {
      setPointH(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointI = (value) => {
    if (value == "11") {
      setPointI(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointJ = (value) => {
    if (value == "7") {
      setPointJ(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointK = (value) => {
    if (value == "8") {
      setPointK(value);
    } else {
      alert("Select correct point");
    }
  };
  const onChangePointL = (value) => {
    if (value == "6") {
      setPointL(value);
    } else {
      alert("Select correct point");
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          height: "82%",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            height: "100%",
            width: "100%",
          }}
        >
          <div
            style={{
              flex: 19,
              border: "solid",
              borderWidth: "1px",
              background: "#e2f97a",
              paddingTop: "15px",
            }}
          >
            <Images />
            <NumberPoints />
            <Points />
            {pointA == "3" && <WireA />}
            {pointB == "1" && <WireB />}
            {pointC == "1" && <WireC />}
            {pointD == "1" && <WireD />}
            {pointE == "2" && <WireE />}
            {pointF == "4" && <WireF />}
            {pointG == "5" && <WireG />}
            {pointH == "10" && <WireH />}
            {pointI == "11" && <WireI />}
            {pointJ == "7" && <WireJ />}
            {pointK == "8" && <WireK />}
            {pointL == "6" && <WireL />}
            <BatteryWire />
          </div>

          <div
            style={{
              flex: 6,
              backgroundColor: "#A9A9A9",
              paddingTop: "20px",
            }}
          >
            <div style={{ marginTop: "5px", marginLeft: "10px" }}>
              <label>Select the correct connecting points</label>
              <MpptTable>
                <tr>
                  <th>Mppt Point</th>
                  <th>Connecting Point</th>
                </tr>
                <tr>
                  <td>A</td>
                  <td>
                    <select
                      value={pointA}
                      onChange={(e) => onChangePointA(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>B</td>
                  <td>
                    <select
                      value={pointB}
                      onChange={(e) => onChangePointB(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>C</td>
                  <td>
                    <select
                      value={pointC}
                      onChange={(e) => onChangePointC(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>D</td>
                  <td>
                    <select
                      value={pointD}
                      onChange={(e) => onChangePointD(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>E</td>
                  <td>
                    <select
                      value={pointE}
                      onChange={(e) => onChangePointE(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>F</td>
                  <td>
                    <select
                      value={pointF}
                      onChange={(e) => onChangePointF(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>G</td>
                  <td>
                    <select
                      value={pointG}
                      onChange={(e) => onChangePointG(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>H</td>
                  <td>
                    <select
                      value={pointH}
                      onChange={(e) => onChangePointH(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>I</td>
                  <td>
                    <select
                      value={pointI}
                      onChange={(e) => onChangePointI(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>J</td>
                  <td>
                    <select
                      value={pointJ}
                      onChange={(e) => onChangePointJ(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>K</td>
                  <td>
                    <select
                      value={pointK}
                      onChange={(e) => onChangePointK(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
                <tr>
                  <td>L</td>
                  <td>
                    <select
                      value={pointL}
                      onChange={(e) => onChangePointL(e.target.value)}
                      style={{ marginLeft: "5px" }}
                    >
                      <TableData />
                    </select>
                  </td>
                </tr>
              </MpptTable>
              <ContinueButton
                style={{ marginLeft: "60px" , marginTop:'15px'}}
                onClick={() => props.onStepChange(4)}
                className="continue-button"
                role="button"
              >
                <i className="fa fa-play" style={{ marginRight: "5px" }}></i>{" "}
                Next
              </ContinueButton>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
