import React from 'react';
import HydroIntroSrc from '../assets/hydro.png';

export function SuccessMessage(props) {
    return (
        <div>
            <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                <img src={HydroIntroSrc} style={{ width: '100%' }} />
                <div style={{ backgroundColor: '#0072b1', position: 'absolute', top: '10%', left: '10%', width: '80%', height: '80%' }}>
                    <div style={{ color: 'white', fontSize: '38px', padding: '40px', textAlign: 'center' }}>
                        <div style={{ textAlign: 'center' }}>Congratulations!
                        </div><br></br>
                        You have achieved the
                        learning outcomes of  "The effect of
                        non-normal incidence for the loss of
                        effective irradiance"</div>
                </div>
            </div>
        </div>
    );
}


