import React from 'react';
import IntroSrc from '../assets/intro2.PNG';

export function SolarHeatDetails(props) {
  return (
    <div>
      <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
        <img src={IntroSrc} style={{ width: '100%' }} />
        <div style={{
          position: 'absolute', top: '5%', left: '10%', border: 'solid',
          borderWidth: '3px', borderRadius: '10px', borderColor: 'white', width: '80%', height: '90%'
        }}>
          <div style={{ width: '100%', height: '100%', backgroundColor: 'white', opacity: '0.85', position: 'absolute' }}>
            <div style={{ padding: '0px 20px', marginTop: '20px' }} >
              <h2>Theory</h2>
              <p style={{ fontFamily: 'Arial', fontSize: '19px', marginTop: '20px', display: 'block', lineHeight: '20px' }}>
                Energy collection by circulating water through the flat plate solar thermal collector (Qu) depends on the collector parameters FRUL, FRη0, which are dependent on the heat carrier fluid mass flow rate through the collector, collector area (Ac) and also several external parameters such as hour of the day (ω), insolation on the collector plane (IT), collector orientation, azimuth (γ) and collector tilt to the horizontal (β). The hourly insolation on the collector plane comes as a contribution of beam, diffuse and ground reflected components of solar radiation on a hourly time scale for particular day. The day has been selected as the 21st June, the longest day for the northern hemisphere.
              </p>
              {/* <h2>Lab Goals</h2> */}

            </div>

          </div>
          <div style={{ position: 'absolute', left: '42%', bottom: '20px' }}>
            <button onClick={() => props.onStepChange(3)} className="button-solarHeat" role="button"><i className="fa fa-play" style={{ marginRight: '5px' }} ></i>  Next</button>
          </div>
        </div>
      </div>

    </div>
  );
}


