import React from 'react';
// import a from "../assets/hydro.mp4"

export function HydroDetails(props) {
  return (
    <div>
      <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.0rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
        <video width="1020" height="580" controls >
          <source src="/hydro.mp4" type="video/mp4" />
        </video>
      </div>

      <div style={{ position: 'absolute', right: '50px', bottom: '100px' }}>
        <button onClick={() => props.onStepChange(3)} className="button-next button-hydro" role="button"><i className="fa fa-play" style={{ marginRight: '5px' }} ></i>  Next</button>
      </div>
    </div>
  );
}


