import React from 'react';
import HydroIntroSrc from '../assets/hydro.png';

export function FailMessage(props) {
    return (
        <div>
            <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                <img src={HydroIntroSrc} style={{ width: '100%' }} />
                <div style={{ backgroundColor: '#e57373', position: 'absolute', top: '10%', left: '10%', width: '80%', height: '80%' }}>
                    <div style={{ color: 'white', fontSize: '38px', padding: '40px', textAlign: 'center' }}>

                        You need further understanding theory of
                        "The effect of non-normal incidence for the loss of effective irradiance"
                        <br></br>
                        <div style={{ textAlign: 'center' }}>Please go to beginning
                        </div>
                        <button onClick={() => props.onStepChange(2)}  className="button-hydro button-next" role="button">Theory</button>
                    </div>
                </div>
            </div>
        </div>
    );
}
