import React from "react";
import IntroSrc from "./assets/intro.webp";
import { ContinueButton } from "./video.styles";

export function IntroPage(props) {
  return (
    <>
      <div>
        <div
          style={{
            width: "1020px",
            position: "absolute",
            border: "solid",
            borderWidth: "0.1rem",
            borderColor: "white",
            left: "10px",
            bottom: "10px",
          }}
        >
          <img src={IntroSrc} style={{ width: "100%" }} />
        </div>
        <div style={{ position: "absolute", left: "50px", top: "150px" }}>
          <span style={{ color: "white", fontSize: "36px" }}>
            WIND TURBINE INSTALLATION PROCEDURE<br></br>
          </span>
          <div
            style={{
              color: "white",
              fontFamily: "Arial",
              marginTop: "10px",
              width: "330px",
              display: "block",
              lineHeight: "24px",
            }}
          >
            To provide an overview on the basic steps In Wind Turbine
            Installation
          </div>
        </div>
        <div style={{ position: "absolute", right: "50px", bottom: "50px" }}>
          <ContinueButton
            onClick={() => props.onStepChange(2)}
            className="continue-button"
            role="button"
          >
            <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Begin
          </ContinueButton>
        </div>
      </div>
    </>
  );
}
