import React, { useState } from "react";
import { NavBar } from "../_components/NavBar";
import { MainConatiner } from "../_components/mainContainer.style";
import { HydroIntro } from "./components/intro";
import { HydroPlantPractical } from "./components/practical";

export function PowerPlantMainPage(props) {
  const [visibleStep, setVisibleStep] = useState(3);
  const onStepChange = (step) => {
    setVisibleStep(step);
  };
  return (
    <MainConatiner theme={"hydro"}>
      <NavBar onStepChange={onStepChange} />
      {visibleStep == 1 && <HydroIntro onStepChange={onStepChange} />}
      {visibleStep == 3 && <HydroPlantPractical onStepChange={onStepChange} />}
    </MainConatiner>
  );
}
