import React from "react";
import MpptSrc from "../assets/mppt.png";
import TurbineSrc from "../assets/turbine.png";
import DCSrc from "../assets/dc.png";
import ACSrc from "../assets/ac.png";
import DumpLoadSrc from "../assets/dumpLoad.png";
import InverterSrc from "../assets/inverter.png";
import BatterySrc from "../assets/battery.png";
import SolarSrc from "../assets/solar.png";

export function Images(props) {
  return (
    <>
      <img
        src={TurbineSrc}
        style={{
          position: "absolute",
          top: "0px",
          left: "0px",
          width: "178px",
        }}
      />

      <img
        src={SolarSrc}
        style={{
          position: "absolute",
          top: "180px",
          left: "220px",
          width: "80px",
        }}
      />
      <img
        src={MpptSrc}
        style={{
          position: "absolute",
          top: "150px",
          left: "340px",
          width: "150px",
        }}
      />
      <img
        src={DumpLoadSrc}
        style={{
          position: "absolute",
          top: "30px",
          left: "550px",
          width: "110px",
        }}
      />

      <img
        src={DCSrc}
        style={{
          position: "absolute",
          top: "140px",
          left: "550px",
          width: "180px",
        }}
      />

      <img
        src={BatterySrc}
        style={{
          position: "absolute",
          bottom: "40px",
          left: "20px",
          width: "210px",
        }}
      />

      <img
        src={InverterSrc}
        style={{
          position: "absolute",
          bottom: "40px",
          left: "340px",
          width: "120px",
        }}
      />

      <img
        src={ACSrc}
        style={{
          position: "absolute",
          bottom: "15px",
          left: "450px",
          width: "300px",
        }}
      />
    </>
  );
}
