import React, { useState } from 'react';
import HydroIntroSrc from '../assets/hydro.png';

export function Questions(props) {
    const [checked1, setChecked1] = useState(false);
    const [checked2, setChecked2] = useState(false);
    const [checked3, setChecked3] = useState(false);
    const handleOnChange1 = () => {
        setChecked1(!checked1);
    };
    const handleOnChange2 = () => {
        setChecked2(!checked2);
    };
    const handleOnChange3 = () => {
        setChecked3(!checked3);
    };
    const onSubmitClicked = () => {
        if (checked2 && checked3 && (!checked1)) {
            props.onStepChange(5);
        }
        else {
            props.onStepChange(6);
        }
    }
    return (
        <div>
            <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                <img src={HydroIntroSrc} style={{ width: '100%' }} />
                <div style={{ display: 'flex', flexDirection: "column", position: 'absolute', top: '0%', left: '0%', width: '100%', height: '100%' }}>
                    <div style={{ flex: 2, backgroundColor: 'black', opacity: '0.7', color: 'white' }} >
                        <div style={{ textAlign: 'center',marginTop:'20px' }} >
                            <h3>PLEASE SELECT ALL THE STATEMENTS <br></br> THAT ARE TRUE </h3>
                        </div>
                    </div>
                    <div style={{ flex: 10 }} >
                        <div style={{ width: '80%', position: 'absolute', left: '10%' }}>
                            <div style={{ display: 'flex', flexDirection: "row", marginTop: '70px' }}>
                                <div style={{ flex: 5, backgroundColor: 'white' }}>
                                    <div style={{ textAlign: 'left', border: 'solid', borderWidth: '0.2rem', borderColor: 'black', padding: '8px' }}>
                                        <div style={{ fontSize: '19px', fontWeight: '450' }}>Lambert Cosine Law can be used to calculate the loss of power output due to geometry</div>
                                    </div>
                                </div>
                                <div style={{ flex: 1, position: 'relative' }}>
                                    {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                                    <div style={{ position: 'absolute', bottom: '30%', left: '30%' }} >
                                        <input type="checkbox" checked={checked1} onChange={handleOnChange1} style={{ width: '25px', height: '25px' }} id="html"></input>
                                    </div>
                                </div>
                            </div>
                            <div style={{ display: 'flex', flexDirection: "row", marginTop: '40px' }}>
                                <div style={{ flex: 5, backgroundColor: 'white' }}>
                                    <div style={{ textAlign: 'left', border: 'solid', borderWidth: '0.2rem', borderColor: 'black', padding: '8px' }}>
                                        <div style={{ fontSize: '19px', fontWeight: '450' }}>Lambert Cosine Law can be used to calculate the loss of effective irradiance due to geometry</div>
                                    </div>
                                </div>
                                <div style={{ flex: 1, position: 'relative' }}>
                                    {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                                    <div style={{ position: 'absolute', bottom: '30%', left: '30%' }} >
                                        <input type="checkbox" checked={checked2} onChange={handleOnChange2} style={{ width: '25px', height: '25px' }} id="html"></input>
                                    </div>
                                </div>
                            </div>
                            <div style={{ display: 'flex', flexDirection: "row", marginTop: '40px' }}>
                                <div style={{ flex: 5, backgroundColor: 'white' }}>
                                    <div style={{ textAlign: 'left', border: 'solid', borderWidth: '0.2rem', borderColor: 'black', padding: '8px' }}>
                                        <div style={{ fontSize: '19px', fontWeight: '450' }}>Lambert Cosine Law indicates that the reduction of irradiance is proportional to cosine(AOI), where Normal Incidence = AOI</div>
                                    </div>
                                </div>
                                <div style={{ flex: 1, position: 'relative' }}>
                                    {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                                    <div style={{ position: 'absolute', bottom: '30%', left: '30%' }} >
                                        <input type="checkbox" checked={checked3} onChange={handleOnChange3} style={{ width: '25px', height: '25px' }} id="html"></input>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div style={{ position: 'absolute', right: '50px', bottom: '40px' }}>
                            <button onClick={onSubmitClicked}  className="button-hydro button-next" role="button">  Submit</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}


