import React, { useState } from 'react';
import HydroPlantSrc from "../assets/control2.jpg";
import '../hydro.css';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import { useEffect } from 'react';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const options = {
  responsive: true,
  plugins: {
    legend: {
      position: 'top',
    },
    title: {
      display: true,
      text: 'Frequency Chart',
    },
  },
  scales: {
    xAxes: {
      display: true,

    },
    yAxes: {
      title: {
        display: true,
        text: 'Frequency Hz'
      },
      min: 49,
      max: 51,
      ticks: {
        callback: function (value, index, values) {
          return value;
        },
        stepSize: 0.25
      }
    }
  },
};

const optionsGenerator = {
  responsive: true,
  plugins: {
    legend: {
      position: 'top',
    },
    title: {
      display: true,
      text: 'Generator RPM Chart',
    },
  },
  scales: {
    xAxes: {
      display: true,

    },
    yAxes: {
      title: {
        display: true,
        text: 'RPM'
      },
      min: 735,
      max: 765,
      ticks: {
        callback: function (value, index, values) {
          return value;
        },
        stepSize: 3.75
      }
    }
  },
};

const labels = ['25%', '50%', '75%', '100%'];


const initialData = {
  labels,
  datasets: [
    {
      label: '1000 KW',
      data: [],
      borderColor: 'rgb(255, 99, 132)',
      backgroundColor: 'rgba(255, 99, 132, 0.5)',
    },
    {
      label: '2000 KW',
      data: [],
      borderColor: 'rgb(53, 162, 235)',
      backgroundColor: 'rgba(53, 162, 235, 0.5)',
    },
    {
      label: '3000 KW',
      data: [],
      backgroundColor: "rgba(75,192,192,0.2)",
      borderColor: "rgba(75,192,192,1)"
    },
    {
      label: '4000 KW',
      data: [],
      backgroundColor: "rgba(255, 159, 64, 0.2)",
      borderColor: "rgba(255, 159, 64,1)"
    },
  ]

}
const initialDataGenerator = {
  labels,
  datasets: [
    {
      label: '1000 KW',
      data: [],
      borderColor: 'rgb(255, 99, 132)',
      backgroundColor: 'rgba(255, 99, 132, 0.5)',
    },
    {
      label: '2000 KW',
      data: [],
      borderColor: 'rgb(53, 162, 235)',
      backgroundColor: 'rgba(53, 162, 235, 0.5)',
    },
    {
      label: '3000 KW',
      data: [],
      backgroundColor: "rgba(75,192,192,0.2)",
      borderColor: "rgba(75,192,192,1)"
    },
    {
      label: '4000 KW',
      data: [],
      backgroundColor: "rgba(255, 159, 64, 0.2)",
      borderColor: "rgba(255, 159, 64,1)"
    },
  ],
};



export function HydroPractical(props) {
  const [ricketGatePer, setRicketGatePer] = useState(0);
  const [demand, setDemand] = useState(2000);
  const [load, setLoad] = useState(1000);
  const [gateEndy, setGateEndY] = useState(274);
  const [frequencyData, setFrequencyData] = useState(initialData);
  const [generatorData, setGeneratorData] = useState(initialDataGenerator);

  useEffect(() => {
    setFrequencyData({
      labels,
      datasets: [
        {
          label: '1000 KW',
          data: [],
          borderColor: 'rgb(255, 99, 132)',
          backgroundColor: 'rgba(255, 99, 132, 0.5)',
        },
        {
          label: '2000 KW',
          data: [],
          borderColor: 'rgb(53, 162, 235)',
          backgroundColor: 'rgba(53, 162, 235, 0.5)',
        },
        {
          label: '3000 KW',
          data: [],
          backgroundColor: "rgba(75,192,192,0.2)",
          borderColor: "rgba(75,192,192,1)"
        },
        {
          label: '4000 KW',
          data: [],
          backgroundColor: "rgba(255, 159, 64, 0.2)",
          borderColor: "rgba(255, 159, 64,1)"
        },
      ],
    });
    setGeneratorData({
      labels,
      datasets: [
        {
          label: '1000 KW',
          data: [],
          borderColor: 'rgb(255, 99, 132)',
          backgroundColor: 'rgba(255, 99, 132, 0.5)',
        },
        {
          label: '2000 KW',
          data: [],
          borderColor: 'rgb(53, 162, 235)',
          backgroundColor: 'rgba(53, 162, 235, 0.5)',
        },
        {
          label: '3000 KW',
          data: [],
          backgroundColor: "rgba(75,192,192,0.2)",
          borderColor: "rgba(75,192,192,1)"
        },
        {
          label: '4000 KW',
          data: [],
          backgroundColor: "rgba(255, 159, 64, 0.2)",
          borderColor: "rgba(255, 159, 64,1)"
        },
      ],
    });
  }, []);

  const addRicketGatePer = () => {
    if (ricketGatePer < 100) {
      setRicketGatePer(parseInt(ricketGatePer) + 25);
      calculateGateEndY(parseInt(ricketGatePer) + 25);
      drawFrequencyChart(parseInt(ricketGatePer) + 25);
      drawGeneratorChart(parseInt(ricketGatePer) + 25);
    }
  }

  const minusRicketGatePer = () => {
    if (ricketGatePer > 0) {
      setRicketGatePer(parseInt(ricketGatePer) - 25);
      calculateGateEndY(parseInt(ricketGatePer) - 25);
      drawFrequencyChart(parseInt(ricketGatePer) - 25);
      drawGeneratorChart(parseInt(ricketGatePer) - 25);
    }
  }

  const calculateGateEndY = (percentage) => {
    if (percentage == 100) {
      setGateEndY(213)
    } else if (percentage == 75) {
      setGateEndY(229)
    }
    else if (percentage == 50) {
      setGateEndY(243)
    }
    else if (percentage == 25) {
      setGateEndY(257)
    }
    else if (percentage == 0) {
      setGateEndY(274)
    }
  }

  const drawGeneratorChart = (percentage) => {
    let tempData = [...generatorData.datasets];

    if (load == 1000) {
      if (percentage == 100) {
        tempData[0].data[3] = 761.25;
      } else if (percentage == 75) {
        tempData[0].data[2] = 757.5;
      }
      else if (percentage == 50) {
        tempData[0].data[1] = 753.75;
      }
      else if (percentage == 25) {
        tempData[0].data[0] = 750;
      }
    } else if (load == 2000) {
      if (percentage == 100) {
        tempData[1].data[3] = 757.5;
      } else if (percentage == 75) {
        tempData[1].data[2] = 753.75;
      }
      else if (percentage == 50) {
        tempData[1].data[1] = 750;
      }
      else if (percentage == 25) {
        tempData[1].data[0] = 746.25;
      }
    } else if (load == 3000) {
      if (percentage == 100) {
        tempData[2].data[3] = 753.75;
      } else if (percentage == 75) {
        tempData[2].data[2] = 750;
      }
      else if (percentage == 50) {
        tempData[2].data[1] = 746.25;
      }
      else if (percentage == 25) {
        tempData[2].data[0] = 742.5;
      }
    } else if (load == 4000) {
      if (percentage == 100) {
        tempData[3].data[3] = 750;
      } else if (percentage == 75) {
        tempData[3].data[2] = 746.25;
      }
      else if (percentage == 50) {
        tempData[3].data[1] = 742.5;
      }
      else if (percentage == 25) {
        tempData[3].data[0] = 738.75;
      }
    }
    setGeneratorData({ labels, datasets: tempData });
  }

  const drawFrequencyChart = (percentage) => {
    let tempData = [...frequencyData.datasets];

    if (load == 1000) {
      if (percentage == 100) {
        tempData[0].data[3] = 50.75;
      } else if (percentage == 75) {
        tempData[0].data[2] = 50.5;
      }
      else if (percentage == 50) {
        tempData[0].data[1] = 50.25;
      }
      else if (percentage == 25) {
        tempData[0].data[0] = 50;
      }
    } else if (load == 2000) {
      if (percentage == 100) {
        tempData[1].data[3] = 50.5;
      } else if (percentage == 75) {
        tempData[1].data[2] = 50.25;
      }
      else if (percentage == 50) {
        tempData[1].data[1] = 50;
      }
      else if (percentage == 25) {
        tempData[1].data[0] = 49.75;
      }
    } else if (load == 3000) {
      if (percentage == 100) {
        tempData[2].data[3] = 50.25;
      } else if (percentage == 75) {
        tempData[2].data[2] = 50;
      }
      else if (percentage == 50) {
        tempData[2].data[1] = 49.75;
      }
      else if (percentage == 25) {
        tempData[2].data[0] = 49.5;
      }
    } else if (load == 4000) {
      if (percentage == 100) {
        tempData[3].data[3] = 50;
      } else if (percentage == 75) {
        tempData[3].data[2] = 49.75;
      }
      else if (percentage == 50) {
        tempData[3].data[1] = 49.5;
      }
      else if (percentage == 25) {
        tempData[3].data[0] = 49.25;
      }
    }
    setFrequencyData({ labels, datasets: tempData });
  }

  return (
    <div>
      <div style={{ width: '1020px', height: '83%', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
        <div style={{ display: 'flex', flexDirection: "row", height: '100%', width: '100%' }}>
          <div style={{ flex: 8, backgroundColor: 'rgb(52, 159, 199)', display: 'flex', flexDirection: "column", borderRight: 'solid', borderWidth: '1px' }} >


            <div style={{ flex: 9, backgroundColor: 'white' }} >
              <svg width="620" height="400" >
                <image x="35" y="18" height="400" xlinkHref={HydroPlantSrc} />
                <line x1={136} y1={132} x2={136} y2={gateEndy} style={{ stroke: 'black', strokeWidth: '15' }} />

              </svg>
            </div>
            <div style={{ flex: 3, marginLeft: '25px' }} >
              <div>
                <div style={{ color: 'white', fontSize: '19px', marginTop: '8px' }}>Load - {load} kW</div>
                <div className="form-check form-check-inline">
                  <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1" value={1000}
                    onChange={() => { setLoad(1000); setRicketGatePer(0);setGateEndY(274) }} checked={load == 1000} />
                  <label style={{ color: 'white', fontSize: '15px' }} for="exampleRadios1">
                    1000 KW
                  </label>
                </div>
                <div className="form-check form-check-inline">
                  <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios2" value={2000}
                    onChange={() => { setLoad(2000); setRicketGatePer(0);setGateEndY(274) }} checked={load == 2000} />
                  <label style={{ color: 'white', fontSize: '15px' }} for="exampleRadios2">
                    2000 KW
                  </label>
                </div>
                <div className="form-check form-check-inline">
                  <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1" value={3000}
                    onChange={() => { setLoad(3000); setRicketGatePer(0);setGateEndY(274) }} checked={load == 3000} />
                  <label style={{ color: 'white', fontSize: '15px' }} for="exampleRadios1">
                    3000 KW
                  </label>
                </div>
                <div className="form-check form-check-inline">
                  <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios2" value={4000}
                    onChange={() => { setLoad(4000); setRicketGatePer(0);setGateEndY(274) }} checked={load == 4000} />
                  <label style={{ color: 'white', fontSize: '15px' }} for="exampleRadios2">
                    4000 KW
                  </label>
                </div>
              </div>
              <div style={{ marginTop: '5px' }}>
                <div style={{ color: 'white', fontSize: '19px' }}>Gate Open Percentage - {ricketGatePer}%</div>
                <div className="wicketGateContainer" style={{ marginTop: '4px' }}>
                  <button className="wicketGateContainerButton" id="minus" onClick={minusRicketGatePer}>-</button>
                  <input id="range" min="0" max="100" type="range" step="25" style={{ width: '50%' }} value={ricketGatePer} />
                  <button className="wicketGateContainerButton" id="plus" onClick={addRicketGatePer}>+</button>

                </div>
              </div>
              <div style={{ position: 'absolute', left: '520px', bottom: '20px' }}>
                <button onClick={() => props.onStepChange(4)} className="button-hydro button-next" role="button"><i className="fa fa-play" style={{ marginRight: '5px' }} ></i>  Next</button>
              </div>
            </div>

          </div>
          <div style={{ flex: 5, backgroundColor: 'white' }} >

            <Line options={options} data={frequencyData} height={210} />
            <Line style={{marginTop:'10px'}} options={optionsGenerator} data={generatorData} height={210} />
          </div>
        </div>
      </div>


    </div >
  );
}


