import React, { useEffect, useRef, useState } from "react";
import { Tower } from "./components/tower";
import { Background } from "./components/background";
import { Slider } from "@material-ui/core";
import { calculateTurbinePower } from "./calculation";
import { ContinueButton } from "./windTurbine.styles";

export function Practical(props) {
  const [towerHeight, setTowerHeight] = useState(40);
  const [speed, setSpeed] = useState(0);
  const [bladeWidth, setBladewidth] = useState(20);
  const [bladeMaxWidth, setBladeMaxwidth] = useState(80);
  const [altitude, setAltitude] = useState(0);
  const [power, setPower] = useState(0);
  const onTowerHeightChange = (event, value) => {
    setTowerHeight(value);
    setBladewidth(20);
  };
  const onBladeWidthChange = (event, value) => {
    setBladewidth(value);
  };
  const onAltitudeChange = (event, value) => {
    setAltitude(value);
  };
  const onSpeedChange = (event, value) => {
    setSpeed(value);
  };
  useEffect(() => {
    setBladeMaxwidth(towerHeight / 2);
  }, [towerHeight]);

  useEffect(() => {
    setPower(calculateTurbinePower(speed, towerHeight, bladeWidth, altitude));
  }, [towerHeight, speed, bladeWidth, altitude]);
  return (
    <div>
      <div
        style={{
          width: "1020px",
          height: "82%",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            height: "100%",
            width: "100%",
          }}
        >
          <div
            style={{
              flex: 19,
              border: "solid",
              borderWidth: "1px",
              background: "#bfe4ff",
              overflow: "hidden",
            }}
          >
            <Tower
              towerHeight={towerHeight}
              bladeLength={bladeWidth * 2}
              speed={speed}
              x="590"
              y="880"
            />
            <Background altitude={altitude} />
          </div>

          <div
            style={{
              flex: 6,
              backgroundColor: "#A9A9A9",
              paddingTop: "20px",
            }}
          >
            <div style={{ marginTop: "35px", marginLeft: "10px" }}>
              <div style={{ display: "flex", flexDirection: "column" }}>
                Tower Height : {towerHeight}M
                <Slider
                  size="large"
                  style={{ width: 200 }}
                  min={40}
                  max={160}
                  step={2}
                  value={towerHeight}
                  onChange={onTowerHeightChange}
                  aria-labelledby="discrete-slider-restrict"
                  color="primary"
                />
              </div>

              <div style={{ display: "flex", flexDirection: "column" }}>
                Blade width : {bladeWidth}M
                <Slider
                  size="large"
                  style={{ width: 200 }}
                  min={20}
                  max={bladeMaxWidth}
                  step={1}
                  value={bladeWidth}
                  onChange={onBladeWidthChange}
                  aria-labelledby="discrete-slider-restrict"
                  color="primary"
                />
              </div>
              <div style={{ display: "flex", flexDirection: "column" }}>
                Wind speed : {speed}m/s
                <Slider
                  size="large"
                  style={{ width: 200 }}
                  min={0}
                  max={24}
                  step={0.2}
                  value={speed}
                  onChange={onSpeedChange}
                  aria-labelledby="discrete-slider-restrict"
                  color="primary"
                />
              </div>
              <div style={{ display: "flex", flexDirection: "column" }}>
                Altitude : {altitude}M
                <Slider
                  size="large"
                  style={{ width: 200 }}
                  min={0}
                  max={5000}
                  step={100}
                  value={altitude}
                  onChange={onAltitudeChange}
                  aria-labelledby="discrete-slider-restrict"
                  color="primary"
                />
              </div>
              <div style={{ margin: "50px 0", fontSize: "22px" }}>
                Power :
                {(power / 1000).toLocaleString(undefined, {
                  maximumFractionDigits: 0,
                })}{" "}
                kW
              </div>
              <div style={{ marginLeft: "60px" }}>
                <ContinueButton
                  onClick={() => props.onStepChange(4)}
                  className="continue-button"
                  role="button"
                >
                  <i className="fa fa-play" style={{ marginRight: "5px" }}></i>{" "}
                  Next
                </ContinueButton>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
