import React from "react";
import BioSrc from "../../../assets/bio.png";

export function BioMassIntro(props) {
  return (
    <>
      <div>
        <div
          style={{
            width: "1020px",
            position: "absolute",
            border: "solid",
            borderWidth: "0.1rem",
            borderColor: "white",
            left: "10px",
            bottom: "10px",
          }}
        >
          <img src={BioSrc} style={{ width: "100%" }} />
        </div>
        <div style={{ position: "absolute", left: "50px", top: "150px" }}>
          <span style={{ color: "black", fontSize: "36px" }}>
            Optimization of Bioethanol Production Processes.
            <br></br>
          </span>
          <div
            style={{
              color: "black",
              fontFamily: "Arial",
              marginTop: "10px",
              width: "330px",
              display: "block",
              lineHeight: "24px",
              // backgroundColor: "rgba(0, 0, 0, 0.3)",
            }}
          >
            Learning Outcome: The effect of fermentation processes parameters on
            bioethanol yield.
          </div>
        </div>
        <div style={{ position: "absolute", right: "50px", bottom: "50px" }}>
          <button
            onClick={() => props.onStepChange(2)}
            className="button-solarPV"
            role="button"
          >
            <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Begin
          </button>
        </div>
      </div>
    </>
  );
}
