import React, { useState } from 'react';
import IntroSrc from "./assets/intro.jpg";
import { ContinueButton } from './video.styles';

export function Questions(props) {
  const [checked1, setChecked1] = useState(false);
  const [checked2, setChecked2] = useState(false);
  const [checked3, setChecked3] = useState(false);
  const [checked4, setChecked4] = useState(false);
  const [checked5, setChecked5] = useState(false);
  const handleOnChange1 = () => {
    setChecked1(!checked1);
  };
  const handleOnChange2 = () => {
    setChecked2(!checked2);
  };
  const handleOnChange3 = () => {
    setChecked3(!checked3);
  };
  const handleOnChange4 = () => {
    setChecked4(!checked4);
  };
  const handleOnChange5 = () => {
    setChecked5(!checked5);
  };
  const onSubmitClicked = () => {
    if (checked1 && checked3 && checked4 && !checked2 && !checked5) {
      props.onStepChange(5);
    } else {
      props.onStepChange(6);
    }
  }
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            position: "absolute",
            top: "0%",
            left: "0%",
            width: "100%",
            height: "100%",
          }}
        >
          <div
            style={{
              flex: 2,
              backgroundColor: "black",
              opacity: "0.7",
              color: "white",
            }}
          >
            <div style={{ textAlign: "center", marginTop: "20px" }}>
              <h3>
                Which of the following statements are true regarding the operation of Waste-to-
                Energy (WtE) plants?
              </h3>
            </div>
          </div>
          <div style={{ flex: 10 }}>
            <div style={{ width: "80%", position: "absolute", left: "10%" }}>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  marginTop: "10px",
                }}
              >
                <div style={{ flex: 5, backgroundColor: "white" }}>
                  <div
                    style={{
                      textAlign: "left",
                      border: "solid",
                      borderWidth: "0.2rem",
                      borderColor: "black",
                      padding: "8px",
                    }}
                  >
                    <div style={{ fontSize: "19px", fontWeight: "450" }}>
                      A) Waste processing and pre-treatment involve sorting the waste to remove
                      recyclables and preparing the refuse-derived fuel (RDF) for thermal treatment.
                    </div>
                  </div>
                </div>
                <div style={{ flex: 1, position: "relative" }}>
                  {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                  <div
                    style={{
                      position: "absolute",
                      bottom: "30%",
                      left: "30%",
                    }}
                  >
                    <input
                      type="checkbox"
                      checked={checked1}
                      onChange={handleOnChange1}
                      style={{ width: "25px", height: "25px" }}
                      id="html"
                    ></input>
                  </div>
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  marginTop: "10px",
                }}
              >
                <div style={{ flex: 5, backgroundColor: "white" }}>
                  <div
                    style={{
                      textAlign: "left",
                      border: "solid",
                      borderWidth: "0.2rem",
                      borderColor: "black",
                      padding: "8px",
                    }}
                  >
                    <div style={{ fontSize: "19px", fontWeight: "450" }}>
                      B) The primary thermal treatment method used in WtE plants is cryogenic freezing of
                      waste to extract energy.
                    </div>
                  </div>
                </div>
                <div style={{ flex: 1, position: "relative" }}>
                  {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                  <div
                    style={{
                      position: "absolute",
                      bottom: "30%",
                      left: "30%",
                    }}
                  >
                    <input
                      type="checkbox"
                      checked={checked2}
                      onChange={handleOnChange2}
                      style={{ width: "25px", height: "25px" }}
                      id="html"
                    ></input>
                  </div>
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  marginTop: "10px",
                }}
              >
                <div style={{ flex: 5, backgroundColor: "white" }}>
                  <div
                    style={{
                      textAlign: "left",
                      border: "solid",
                      borderWidth: "0.2rem",
                      borderColor: "black",
                      padding: "8px",
                    }}
                  >
                    <div style={{ fontSize: "19px", fontWeight: "450" }}>
                      C) Energy recovery in WtE plants includes the generation of steam to drive turbines
                      and produce electricity, with the excess heat potentially used in district heating
                      systems.
                    </div>
                  </div>
                </div>
                <div style={{ flex: 1, position: "relative" }}>
                  {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                  <div
                    style={{
                      position: "absolute",
                      bottom: "30%",
                      left: "30%",
                    }}
                  >
                    <input
                      type="checkbox"
                      checked={checked3}
                      onChange={handleOnChange3}
                      style={{ width: "25px", height: "25px" }}
                      id="html"
                    ></input>
                  </div>
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  marginTop: "10px",
                }}
              >
                <div style={{ flex: 5, backgroundColor: "white" }}>
                  <div
                    style={{
                      textAlign: "left",
                      border: "solid",
                      borderWidth: "0.2rem",
                      borderColor: "black",
                      padding: "8px",
                    }}
                  >
                    <div style={{ fontSize: "19px", fontWeight: "450" }}>
                      D) Advanced emission control systems in WtE plants are designed to capture and
                      neutralize pollutants like nitrogen oxides (NOx) and sulfur oxides (SOx) before they
                      are released into the atmosphere.
                    </div>
                  </div>
                </div>
                <div style={{ flex: 1, position: "relative" }}>
                  {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                  <div
                    style={{
                      position: "absolute",
                      bottom: "30%",
                      left: "30%",
                    }}
                  >
                    <input
                      type="checkbox"
                      checked={checked4}
                      onChange={handleOnChange4}
                      style={{ width: "25px", height: "25px" }}
                      id="html"
                    ></input>
                  </div>
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  marginTop: "10px",
                }}
              >
                <div style={{ flex: 5, backgroundColor: "white" }}>
                  <div
                    style={{
                      textAlign: "left",
                      border: "solid",
                      borderWidth: "0.2rem",
                      borderColor: "black",
                      padding: "8px",
                    }}
                  >
                    <div style={{ fontSize: "19px", fontWeight: "450" }}>
                      E) The residual ash from the combustion process in WtE plants is generally deemed
                      non-toxic and is directly reused without further treatment.
                    </div>
                  </div>
                </div>
                <div style={{ flex: 1, position: "relative" }}>
                  {/* <Checkbox  color='default' style={{ backgroundColor: 'white'}}/>    */}
                  <div
                    style={{
                      position: "absolute",
                      bottom: "30%",
                      left: "30%",
                    }}
                  >
                    <input
                      type="checkbox"
                      checked={checked5}
                      onChange={handleOnChange5}
                      style={{ width: "25px", height: "25px" }}
                      id="html"
                    ></input>
                  </div>
                </div>
              </div>


            </div>
            <div
              style={{ position: "absolute", right: "50px", bottom: "40px" }}
            >
              <ContinueButton
                onClick={onSubmitClicked}
                className="continue-button"
                role="button"
              >
                Submit
              </ContinueButton>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}


