import React from "react";
import BananasSrc from "../../assets/bananas.jpg";
import PapayaSrc from "../../assets/papaya.jpg";
import JackFrc from "../../assets/jackfruit.webp";
import { Card, CardContainer, SelectingContainer } from "./practical.styles";

export function SelectFruit(props) {
  return (
    <SelectingContainer>
      <h4 style={{ textAlign: "center", marginBottom: "25px" }}>
        Select Fruit
      </h4>
      <CardContainer>
        <Card
          onClick={() => {
            props.setSelectedFruit("Banana");
            props.changeCurrentStep();
          }}
        >
          <img src={BananasSrc} width={"100%"} />
        </Card>
        <Card
          onClick={() => {
            props.setSelectedFruit("Papaya");
            props.changeCurrentStep();
          }}
        >
          <img src={PapayaSrc} width={"100%"} />
        </Card>
        <Card
          onClick={() => {
            props.setSelectedFruit("Jack");
            props.changeCurrentStep();
          }}
        >
          <img src={JackFrc} width={"100%"} />
        </Card>
      </CardContainer>
    </SelectingContainer>
  );
}
