import React from 'react';
import { useNavigate } from 'react-router-dom';
import LogoImgSrc from "../../assets/logo.png";

export function NavBar(props) {
    const navigate = useNavigate();
    return (
        <div className='app-header-container'>
            <header className='header'>
                <div className="logo" onClick={() => { navigate("/") }}><img src={LogoImgSrc} style={{ height: '65px' }} /></div>
                <ul className="main-nav" style={{ display: 'flex', marginTop: '15px', cursor: 'pointer' }}>
                    <li onClick={() => props.onStepChange(1)}><h3>Introduction</h3></li>
                    <li onClick={() => props.onStepChange(2)}><h3>Theory</h3></li>
                    <li onClick={() => props.onStepChange(3)}><h3>Practical</h3></li>
                    <li onClick={() => { }}><h3>Questions</h3></li>
                </ul>
            </header>
        </div>
    );
}