import React from 'react';

export function FailMessage(props) {
    return (
        <div>
            <div style={{ width: '1020px', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                <div style={{ backgroundColor: '#e57373', width: '1015px', height: '580px'  }}>
                    <div style={{ color: 'white', fontSize: '38px', padding: '80px 40px', textAlign: 'center' }}>

                        {props.message}
                        <br></br>
                        <div style={{ textAlign: 'center' }}>Please go to beginning
                        </div>
                        <button onClick={() => props.onStepChange(2)} className="button-34" role="button">Theory</button>
                    </div>
                </div>
            </div>
        </div>
    );
}


