import React, { useState } from "react";
import IntroSrc from "./assets/intro.webp";
import { ContinueButton } from "./video.styles";

export function Details(props) {
  const [step, setStep] = useState(1);
  const onClickContinue = () => {
    if (step === 1) {
      setStep(2);
    } else {
      props.onStepChange(3);
    }
  };
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={IntroSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              {step === 1 && (<><p>
                Setting up a solar power plant involves several critical steps to ensure its efficiency, safety, and
                longevity. Below is a comprehensive guide on the typical stages involved in establishing a solar
                power plant:
              </p>
                <p>
                  Planning and Site Assessment: Evaluate the location for solar potential, considering factors
                  such as sunlight availability, space for panels, and any shading from trees or buildings. This step
                  may involve using solar mapping tools and conducting on-site surveys.
                </p>
                <p>
                  Feasibility Study: Conduct a detailed study to assess the technical, economic, and
                  environmental feasibility of the project. This includes estimating the plant's capacity, expected
                  energy generation, costs, and return on investment.
                </p>
                <p>
                  Obtaining Permits and Approvals: Secure all necessary permits and approvals from local
                  authorities, which may include land use permits, building permits, and electrical connection
                  approvals. Environmental impact assessments might also be required.
                </p>
                <p>
                  Design and Planning: Design the solar power system, selecting the appropriate technology
                  (photovoltaic (PV) panels, concentrated solar power (CSP), etc.), layout, and components such
                  as inverters and mounting systems. This stage involves detailed technical and engineering
                  planning to maximize efficiency and reliability.
                </p></>)}

              {step === 2 && (<>
                <p>
                  Financing: Secure financing for the project through loans, grants, investments, or other funding
                  sources. This step involves presenting the feasibility study and business plan to potential
                  financiers or investors.
                </p>
                <p>
                  Procurement: Procure all necessary equipment and materials for the solar power plant. This
                  includes solar panels, inverters, mounting structures, transformers, and cabling. Selecting
                  quality components from reliable suppliers is crucial for the plant's long-term performance.
                </p>
                <p>
                  Construction and Installation: Begin the physical construction of the solar power plant. This
                  includes preparing the site, installing the solar panels and mounting systems, electrical wiring,
                  inverters, and connecting the system to the grid or storage solutions.
                </p>
                <p>
                  Testing and Commissioning (Quality Assurance) : After installation, conduct thorough testing
                  of the entire system to ensure it operates correctly and efficiently. This step includes checking
                  the electrical connections, inverter settings, and the performance of the solar panels.
                </p>
              </>)}
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <ContinueButton
              onClick={() => onClickContinue()}
              className="continue-button"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i>
              {step === 1 && <h>Continue</h>}
              {step === 2 && <h>Next</h>}
            </ContinueButton>
          </div>
        </div>
      </div>
    </div>
  );
}
