import React, { useState } from "react";
import { Practical } from "./components/practicalx";

import { SolarPVIntro } from "./components/intro";
import { SolarPVDetails } from "./components/details";
import { Questions } from "./components/questions";
import { NavBar } from "../_components/NavBar";
import "./circuit.css";
import { SuccessMessage } from "../_components/SuccessMessage";
import { FailMessage } from "../_components/FailMessage";

export function SolarPVCircuitMainPage(props) {
  const [visibleStep, setVisibleStep] = useState(1);
  const onStepChange = (step) => {
    setVisibleStep(step);
  };
  return (
    <div className="app-main-tile theme-circuit">
      <NavBar onStepChange={onStepChange} />
      {visibleStep == 1 && <SolarPVIntro onStepChange={onStepChange} />}
      {visibleStep == 2 && <SolarPVDetails onStepChange={onStepChange} />}
      {visibleStep == 3 && <Practical onStepChange={onStepChange} />}
      {visibleStep == 4 && <Questions onStepChange={onStepChange} />}
      {visibleStep == 5 && (
        <SuccessMessage
          message='You have achieved the
                learning outcomes of  "Basic Solar Power Circuit"'
          isSuccess={true}
        />
      )}
      {visibleStep == 6 && (
        <FailMessage
          message='You need further understanding theory of
                "Basic Solar Power Circuit"'
          onStepChange={onStepChange}
          isSuccess={false}
        />
      )}
    </div>
  );
}
