import React from "react";
import { Card, CardContainer, SelectingContainer } from "./practical.styles";
import { styled } from "../../../../stitches.config";

export function SelectPH(props) {
  return (
    <SelectingContainer>
      <h4 style={{ textAlign: "center", marginBottom: "25px" }}>
        Select pH Value
      </h4>
      <CardContainer>
        <PHCard
          style={{ backgroundColor: "#d4e157" }}
          onClick={() => {
            props.setSelectedPH("4.3");
            props.changeCurrentStep();
          }}
        >
          <PHText>4.3</PHText>
        </PHCard>
        <PHCard
          style={{ backgroundColor: "#9ccc65" }}
          onClick={() => {
            props.setSelectedPH("5.0");
            props.changeCurrentStep();
          }}
        >
          <PHText>5.0</PHText>
        </PHCard>
        <PHCard
          style={{ backgroundColor: "#7cb342" }}
          onClick={() => {
            props.setSelectedPH("5.7");
            props.changeCurrentStep();
          }}
        >
          <PHText>5.7</PHText>
        </PHCard>
      </CardContainer>
    </SelectingContainer>
  );
}

const PHText = styled("h2", {
  textAlign: "center",
  margin: "auto",
});

const PHCard = styled(Card, {
  backgroundColor: "#ef5350",
  display: "flex",
  alignItems: "center",
});
